import './RegisterPicture.scss';
import { useState, useCallback, useRef, useEffect } from 'react';
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';

function base64ToStream(base64) {
  let Readable = require('stream').Readable;
  const imgBuffer = Buffer.from(base64, 'base64')
  let s = new Readable();
  s.push(imgBuffer)   
  s.push(null) 
  return s;
}

function RegisterPicture({ setStep, step, icon, setIcon }) {
  
  const nextStep = (e) => {
    e.preventDefault();

    setStep(step + 1);
  }

  const [upImg, setUpImg] = useState();
  const imgRef = useRef(null);
  const previewCanvasRef = useRef(null);
  const [crop, setCrop] = useState({ unit: '%', width: 30, aspect: 9 / 9 });
  const [completedCrop, setCompletedCrop] = useState(null);

  function dl(canvas, crop) {
    if (!crop || !canvas) {
      return;
    }
    canvas.toBlob((blob) => {
        let reader = new FileReader();
        reader.readAsDataURL(blob); 
        reader.onloadend = () => {
          setIcon(reader.result);
          console.log(base64ToStream(reader.result));
          setUpImg(null);
        }
      },'image/png',1);
  }

  const onSelectFile = (e) => {
    if(e.target.files[0].size > 3_000_000){
      alert("Votre photo ne doit pas dépasser 3mo.");
      e.target.value = "";
      return;
   };

    if (e.target.files && e.target.files.length > 0) {
      const reader = new FileReader();
      reader.addEventListener('load', () => setUpImg(reader.result));
      reader.readAsDataURL(e.target.files[0]);
    }
  };

  const onLoad = useCallback((img) => {
    imgRef.current = img;
  }, []);

  useEffect(() => {
    if (!completedCrop || !previewCanvasRef.current || !imgRef.current) {
      return;
    }

    const image = imgRef.current;
    const canvas = previewCanvasRef.current;
    const crop = completedCrop;

    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    const ctx = canvas.getContext('2d');
    const pixelRatio = window.devicePixelRatio;

    canvas.width = crop.width * pixelRatio * scaleX;
    canvas.height = crop.height * pixelRatio * scaleY;

    ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
    ctx.imageSmoothingQuality = 'high';

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * scaleX,
      crop.height * scaleY
    );
  }, [completedCrop]);

  return (
<form onSubmit={nextStep}>
  <h2>Ajouter une photo de profil</h2>
  
  <input type="file" accept="image/*" onChange={onSelectFile} />
  
  {upImg && <div className="register-crop">
      <ReactCrop
        src={upImg}
        onImageLoaded={onLoad}
        crop={crop}
        onChange={(c) => setCrop(c)}
        onComplete={(c) => setCompletedCrop(c)}
      />

      <canvas ref={previewCanvasRef} style={{ width: Math.round(completedCrop?.width ?? 0), height: Math.round(completedCrop?.height ?? 0), display: "none"}}/>
      
      <div className="register-crop-button">
        <button className="btn btn-grey" type="button" onClick={() => setUpImg(null)}>Retour</button>
        <button className="btn" type="button" disabled={!completedCrop?.width || !completedCrop?.height} 
        onClick={() => dl(previewCanvasRef.current, completedCrop) }> Confirmer</button>
      </div>

    </div>}

    {(icon && icon !== "") && <img className="register-img" src={icon} alt="preview" />
    }

    <div className="register-button">
      <div className="btn btn-grey" onClick={() => setStep(step - 1)}>Précédent</div>
      <button className="btn" type="submit">Suivant</button>
      <div className="btn btn-grey" onClick={() => {
        setStep(step + 1);
      }}>Passer</div>
    </div>
  </form>
  );
}

export default RegisterPicture;
