import fetchData from 'utils/fetchData';

class Class {

    static data = [];

    /* ---- constructor ---- */
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id;

        this.department_id = opt.department_id;
        this.semester_id = opt.semester_id;

        this.title = opt.title || "unknown";
        this.ics_link = opt.ics_link || "";
    }

    static getClassById = (id) => {
        return Class.data.find(x => x.id === id);
    }

    static getAll = async () => {
        const data = await fetchData({ url: "api/class" });
        Class.data = data.map(element => new Class(element));
    }

}

export default Class;