
import { useState, useEffect } from 'react';
import TimetableCard from 'components/TimetableCard/TimetableCard';
import fetchData from 'utils/fetchData';

function CalendarList() {
  const [rooms, setRooms] = useState([]);
  const [type, setType] = useState("GROUP");
  const [typeRoom, setTypeRoom] = useState("");
  const [title, setTitle] = useState("");

  useEffect(() => {
    const fetchRooms = async () => {
      const data = await fetchData({
        url: "api/calendar",
      })

      setRooms(data);
    }

    fetchRooms();
  }, [])

  const search = async e => {
    e.preventDefault();

  }

  const DisplayRooms = () => {

    return rooms.map((item, index) => {
      if (title != "" && !item.title.toLowerCase().includes(title.toLowerCase())) return null;
      
      if (type == "ROOM" && item.type.substring(0, 4) == "ROOM") {
        if (typeRoom != "" && item.type != typeRoom) return null;
      } else if (item.type != type) return null;


      
      return <TimetableCard key={index} timetable={item} />
    })
  }

  return <div className={'page calendarsearch'}>
    <div className='box'>
      <form onSubmit={search}>

      <span style={{flexDirection: "column", width: "100%"}}>
        <label>Nom de l'emploi du temps</label>
        <input 
        style={{ width: "100%", maxWidth: "500px"}} 
        type="text" 
        onChange={e => setTitle(e.target.value)}
        value={title}
        />
      </span>

      <span>
        <span>
          <label>Type d'emploi du temps</label>
          <select onChange={e => setType(e.target.value)} value={type}>
            <option value={""}>Choix</option>
            <option value={"PROF"}>Professeur</option>
            <option value={"GROUP"}>Groupe</option>
            <option value={"ROOM"}>Salle</option>
          </select>
        </span>

        {type == "ROOM" && <span>
          <label>Type de salle</label>
          <select onChange={e => setTypeRoom(e.target.value)} value={typeRoom}>
            <option value={""}>Tout type</option>
            <option value={"ROOM_INFO"}>Salle informatique</option>
            <option value={"ROOM_TD"}>Salle de TD / réunion</option>
            <option value={"ROOM_OTHER"}>Autres type de salle</option>
          </select>
        </span>}

      </span>

      </form>
    </div>


    {(rooms.length != 0) && <div className='rooms'>
      <DisplayRooms />
    </div>}


  </div>
}

export default CalendarList;
