import {  AiOutlineSearch  } from "react-icons/ai";
import { Link } from "react-router-dom";
import { RiQuillPenFill } from 'react-icons/ri';
import { useEffect, useState, useContext } from "react";
import { useLocation, useHistory } from "react-router";
import { useParams } from "react-router-dom";

import './Forum.scss';
import Aside from 'components/Aside/Aside';

import TopicCard from "./Topic/Topic"
import TopicMessage from "./TopicMessage/TopicMessage";
import { Category, Topic, User, Class } from 'models';
import { UserContext } from "Context";

function Forum() {
  const history = useHistory();
  const location = useLocation();
  const [forum, setForum] = useState(false);
  const [user] = useContext(UserContext);

  const [text, setText] = useState("");

  const searchParams = new URLSearchParams(location.search);
  const tri_value = searchParams.get("tri") || 1;
  const debut_value = searchParams.get("debut") || "";
  const fin_value = searchParams.get("fin") || "";
  const semester_value = searchParams.get("semester") || "";
  const category_value = searchParams.get("category") || "";
  const status_value = searchParams.get("status") || 1;

  const onChange = (e) => {
    const { name, value } = e.target;
    if (!value || value === "") {
      searchParams.delete(name);
    } else {
      searchParams.set(name, value);
    }
    
    history.push("/forum?" + searchParams.toString());
  }

  useEffect(() => {
    const tab = history.location.pathname.split("/");
    if (tab.length === 2) {
      setForum(false);
    } else {
      setForum(tab[2]);
    }

  }, [location])


  const SearchTri = () => {
    
    return <>
        <label for="search-tri">Tri</label>

      <select 
        name="tri" 
        value={tri_value}
        onChange={onChange}>         
          <option value={1}>Tri par dates croissantes</option>
          <option value={2}>Tri par dates décroissantes</option>
          <option value={3}>Tri par notes croissantes</option>
          <option value={4}>Tri par notes décroissantes</option>
      </select>
    </>
  }

  const SearchStatus = () => {
    return <>
      <label>Status</label>
      <select 
        name="status" 
        value={status_value}
        onChange={onChange}>         
          <option value={1}>Non-résolus</option>
          <option value={2}>Résolus</option>
          <option value={3}>Fermé</option>
      </select>
    </>
  }

  const SearchDate = () => {
    return <>
      <label for="input-date">Dernier message</label>
      <div className="filter-date">
          <span>
            <p>Entre le</p> 
            <input 
            value={debut_value}
            name="debut" 
            onChange={onChange} 
            data-dependent-validation='{"from": "date-to", "prop": "max"}' 
            type="date" 
            id="date-from" 
            placeholder="from" />  
          </span>
          <span>
            <p>et le</p> 
            <input 
              value={fin_value}
              name="fin" 
              onChange={onChange} 
              data-dependent-validation='{"from": "date-from", "prop": "min"}' 
              type="date" 
              id="date-to" 
              placeholder="to" 
            /> 
          </span>
      </div>
    </>
  }

  const SearchCategory = () => {
    const BuildSemester = () => {
      const semesters = Category.data.filter((item) => item.category_id === Class.getClassById(user.class_id).department_id);

      return semesters.map((item,index) => <option key={index} value={item.id}>{item.title}</option>)
    }

    const BuildCategory = () => {
      if (semester_value === "") return null;
      const categories = Category.data.filter((item) => item.category_id === parseInt(semester_value));

      return categories.map((item,index) => <option key={index} value={item.id}>{item.title}</option>)
    }

    return <>
      <label htmlFor="search-semester">Catégorie</label>
      <select 
        value={semester_value} 
        name="semester" 
        onChange={e => { 
          searchParams.delete("category");
          history.push("/forum?" + searchParams.toString());
          onChange(e);
        }} id="search-semester">
          <option value="">Selectionner un semestre</option>
          <BuildSemester />
      </select>
      <select disabled={semester_value === ""} value={category_value} name="category" onChange={onChange} id="search-category">
          <option value="">Selectionner une catégorie</option>
          <BuildCategory />
      </select>
    </>
  }
  
  const DisplayTopic = () => {
    let topics = Topic.data;
    let tri = parseInt(tri_value)

    topics.sort((a, b) => b.creation_date - a.creation_date);
    if (tri === 2) topics.sort((b, a) => b.creation_date - a.creation_date);
    if (tri === 3) topics.sort((b, a) => b.mark - a.mark);
    if (tri === 4) topics.sort((a, b) => b.mark - a.mark);

    topics = topics.filter(item => {
      if (category_value !== "" && item.category_id !== parseInt(category_value)) return false;
      if (status_value !== "" && item.status !== parseInt(status_value)) return false;
      if (debut_value !== "" && new Date(debut_value) > item.creation_date) return false;
      if (fin_value !== "" && new Date(fin_value) < item.creation_date) return false;
      if (text !== "" && !item.title.includes(text)) return false
      if (semester_value !== "") {
        if (Category.getCategoryById(item.category_id)?.category_id !== parseInt(semester_value)) {
          return false;
        }
      }
      return true;
    })

    if (!topics.length) {
      return <p>Aucun résulat</p>
    }

    return topics.map((item, index) => {
      return <TopicCard topic={item} key={index} />
    })
  }
  

  return (
    <>
    {!forum ?
    <>
    <div className="page forum">

      <div className="forum-head">

        <Link to={`/createtopic?category=${category_value}&semester=${semester_value}`} className="btn">
          <RiQuillPenFill />
          <p>Créer mon topic</p>
        </Link>

        <div className="forum-search">
          <input value={text} onChange={e => setText(e.target.value)} type="text" autoFocus />
          <AiOutlineSearch />
        </div>
        
      </div>


      <table className="box">
        <DisplayTopic />
      </table>
      
    </div>

    <Aside>
      <div className="filter">
        <h3>Filtre de recherche</h3>

        <form>

        <SearchCategory />

        <SearchStatus />

        <SearchDate />

        <SearchTri />

        <div onClick={() => history.push("/forum")} className="btn">Rénitialiser les filtres</div>
        </form>
      </div>
    </Aside>
    </> : 
      <>
      <TopicMessage id={forum} />
      <Aside />
      </>
    }</>
  );
}



export default Forum;
