import fetchData from "utils/fetchData";

class Favorite {

    static data = [];

    /* ---- constructor ---- */
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id;

        this.category_id = opt.category_id || 1;
        this.user_id = opt.user_id || 1;
    }

    static getFavoritebyUser(user_id) {
        return Favorite.data.filter(item => item.user_id === user_id);
    }

    static isFavorite(user_id, category_id) {
        return Favorite.data.find(item => item.user_id === user_id && item.category_id === category_id);
    }

    static post = async(user_id, category_id) => {
        await fetchData({
            method: 'post',
            url: 'api/favorite',
            data: {
                user_id: user_id,
                category_id: category_id
            }
        })

        Favorite.data.push(new Favorite({ user_id: user_id, category_id: category_id }));
    }

    static delete = async(user_id, category_id) => {
        await fetchData({
            method: 'delete',
            url: 'api/favorite',
            data: {
                user_id: user_id,
                category_id: category_id
            }
        })

        Favorite.data = Favorite.data.filter(item => item.user_id !== user_id || item.category_id !== category_id);
    }

    static getAll = async () => {
        const data = await fetchData({ url: "api/favorite" });
        Favorite.data = data.map(element => new Favorite(element));
    }

}

export default Favorite;