

import { Category } from 'models';
import './CardMark.scss';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import ProgressProvider from './ProgressProvider';
import { useHistory } from 'react-router-dom';

function CardMark({ mark, size, show }) {
    const { grade, category_id, title, max, id } = mark;
    const history = useHistory();
    const category = Category.getCategoryById(parseInt(category_id)) || new Category();

    const endValue = grade;
    const maxValue = max || 20;
    

    return <div onClick={() => { if (id) history.push("/mark/" + id) }} style={{ width: size || 80}} className="mark">
      <ProgressProvider valueStart={0} valueEnd={endValue}>
        {(value) => <CircularProgressbar styles={buildStyles({
        textColor: category.color,
        pathColor: category.color,
        textSize: '25px',
        fontWeight: 500,
        pathTransitionDuration: 0.5,
      })} value={value} maxValue={maxValue} text={`${value}`} />}
    </ProgressProvider>


      
      <p>{show ? category.title : title}</p>
    </div>
}

export default CardMark;
