import './AdminPanel.scss';
import { useState, useEffect } from 'react';
import fetchData from 'utils/fetchData';
import { Class, Category, Color } from 'models';

const get_parents = () => {
  let data = Category.data.filter(item => (item.category_id == 0 || Category.getCategoryById(item.category_id)?.category_id == 0))
  data = data.map(item => {
    const c = Category.getCategoryById(item.category_id);
    let title = item.title
    if (c) title = c.title + " - " + title;
    return {id: item.id, title: title };
  })
  return data;
}

const get_semesters = () => {
  let data = Category.data.filter(item => (Category.getCategoryById(item.category_id)?.category_id == 0))
  data = data.map(item => {
    let title = Category.getCategoryById(item.category_id).title + " - " + item.title;
    return {id: item.id, title: title };
  })
  return data;
}

const DisplayInput = ({ focus, setFocus, keys }) => {
  const data_models = {
    "Color": Color.data,
    "Category": Category.data,
    "Class": Class.data,
    "Parent": get_parents(),
    "Department": Category.data.filter(item => (item.category_id == 0)),
    "Semester": get_semesters()
  }

  return keys.map((element,index) => {
    if (element.type.charAt(0) == "_") {
      const classname = element.type.substring(1);
      const data = data_models[classname] ? data_models[classname] : []

      return <td key={index}><select 
        value={focus[element.key] || ""}  
        onChange={e => setFocus(prev => ({...prev, [element.key]: e.target.value}))} >
        <option value={""}>Choisir</option>
        {data.map((item, index) => <option value={item.id} key={index}>{item.title}</option>)}
      </select></td>
    }

    return <td key={index}><input 
      value={focus[element.key] || ""} 
      onChange={e => setFocus(prev => ({...prev, [element.key]: e.target.value}))} 
      type={element.type || "text"} 
      disabled={(element.key == "id")}
      placeholder={element.key}
      />
    </td>
  })
}

const DisplayTable = ({ keys, link, list, focus, setFocus, setReload, word, attribute }) => {
  return list.map((item, index) => {
    const suppr = async () => {
      await fetchData({ url: link, method: "DELETE", data: item });
      setReload(old => old + 1);
    }

    const save = async () => {
      await fetchData({ url: link, method: "PUT", data: focus });
      setFocus(null);
      setReload(old => old + 1);
    }

    if (focus && focus.id == item.id) return <tr key={index}>
      <DisplayInput focus={focus} setFocus={setFocus} keys={keys} />
      <td onClick={save}><div className='btn'>Sauvegarder</div></td>
      <td onClick={() => setFocus(null)}><div className='btn btn-grey'>Retour</div></td>
    </tr>

    if (attribute != "" && word != "") {
      const data = item[attribute];
      if (data && !data.toString().includes(word)) return null;
    }

    return <tr key={index}>
      {keys.map((element,index) => {
        if (element.type == "img") return <td key={index}><img src={item[element.key]} height={30} /></td>
        return <td key={index}>{item[element.key]}</td>
      })}
      <td onClick={() => setFocus(item)}><div className='btn'>Modifier</div></td>
      <td onClick={suppr}><div className='btn btn-red'>Supprimer</div></td>
    </tr>
  })
}

const DisplayAdd = ({ setReload, newItem, setNewItem, keys, link }) => {
  const add = async () => {
    await fetchData({ url: link, method: "POST", data: newItem });
    setReload(old => old + 1);
    setNewItem({})
  }

  return <tr>
    <DisplayInput setFocus={setNewItem} focus={newItem} keys={keys} />
    <td colSpan={2} onClick={add}><div className='btn'>Ajouter</div></td>
  </tr>
}

function AdminPanel(props) {
  const keys = props.keys;
  const link = props.link;

  const [list, setList] = useState([]);
  const [focus, setFocus] = useState(null);
  const [reload, setReload] = useState(0);
  const [newItem, setNewItem] = useState({});

  const [word, setWord] = useState("");
  const [attribute, setAtribute] = useState("");

  useEffect(() => {
    const fetchEvent = async () => {
      const res = await fetchData({ url: link });
      setList(res);
    }

    fetchEvent();
  }, [reload]);

  const DisplayHead = () => {
    return keys.map((item,index) => {
      const sort = () => {
        let tab = [...list];
        tab.sort((a,b) => {
          if (typeof a[item.key] === 'string' || a[item.key] instanceof String) {
            return a[item.key].localeCompare(b[item.key]);
          }
          return a[item.key] - b[item.key]
        })
        setList([...tab]);
      }
      return <th onClick={sort} key={index}>{item.key}</th>
    })
  }


  return (
    <div className='adminpanel'>

      <div className='head'>
        <span>
          <div onClick={() => props.setIndex(-1)} className='btn'>Retour</div>
        </span>

        <span className='search'>
          <p>Recherche dans </p>
          <select value={attribute} onChange={e => setAtribute(e.target.value)}>
            <option value={""}>attribut</option>
            {keys.map((item, index) => (<option value={item.key} key={index}>{item.key}</option>))}
          </select>
          <p>contient</p>
          <input value={word} onChange={e => setWord(e.target.value)} placeholder='mot clé' type="text" />
        </span>
      </div>

      <div className="adminpanel-container">
        <table className='box'>
            <thead>
                <tr>
                    <DisplayHead />
                    <th colSpan={2}>Action</th>
                </tr>
                <DisplayAdd keys={keys} newItem={newItem} setNewItem={setNewItem} setReload={setReload} link={link} />
            </thead>
            <tbody>
                <DisplayTable 
                keys={keys} 
                link={link} 
                setReload={setReload} 
                focus={focus} 
                setFocus={setFocus} 
                list={list}
                word={word}
                attribute={attribute} 
                />
            </tbody>
        </table>
      </div>

    </div>

  );
}

export default AdminPanel;
