import fetchData from 'utils/fetchData';

class Document {

    static data = [];

    // constructor
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id;

        this.category_id = opt.category_id;
        this.user_id = opt.user_id;

        this.drive_key = opt.drive_key;
        this.title = opt.title || "document";
        this.mimetype = opt.mimetype || "pdf";
        this.creation_date = new Date(opt.creation_date) || new Date();
        this.section = opt.section || 1;
        this.mark = opt.mark || 0;
    }

    // statics methods
    static createDocument = (object) => {
        return false;
    }

    static deleteDocument = (id) => {
        return false;
    }

    static updateDocument = (object) => {
        return false;
    }

    // getters
    static getDocumentById = (id) => {
        return Document.data.find(x => x.id === id);
    }

    static getListByCategory = (category_id) => {
        return Document.data.filter(x => x.category_id === category_id);
    }

    static post = async (document) => {
        const response = await fetchData({
            method: 'post',
            url: 'api/document',
            data: document
        })

        return response;
    }

    static postMark = async(obj) => {
        await fetchData({
            method: 'post',
            url: 'api/rating',
            data: obj
        })

        await Document.getAll();
    }

    static getMarkById = async(props) => {
        const { user_id, document_id } = props;
        const data = await fetchData({
            method: 'get',
            url: `api/rating/document?user_id=${user_id}&document_id=${document_id}`
        })

        let mark = 0;
        if (data[0]) {
            mark = data[0].mark;
        }
        return mark;
    }

    static getAll = async () => {
        const data = await fetchData({ url: "api/document" });
        Document.data = data.map(element => new Document(element));
    }
}

export default Document;