import React from 'react'
import UserIcon from 'components/UserIcon/UserIcon.js'
import "./ConvCard.scss";

import { User } from 'models';
import { Link } from 'react-router-dom';

const ConvCard = ({ notif, active, conversation }) => {
    const { id, user_id } = conversation;
    const thisUser = User.getUserById(user_id);

    return (
        <Link className={active ? "conv-card conv-card--active" : "conv-card"} to={"/private-messages/" + id}>
            <UserIcon user_id={user_id} size={40} noname />
            <h3>{thisUser.first_name}</h3>
            {notif && <div className="notif"></div>}
        </Link>
    )
}

export default ConvCard
