import { Category, Message, Topic } from 'models';
import { useEffect, useState, useContext } from 'react';
import './TopicMessage.scss';
import MessageCard from "components/Message/Message";
import CategoryCard from 'components/CategoryCard/CategoryCard';
import { IoChevronBack } from 'react-icons/io5';
import { useHistory } from 'react-router';
import { AiFillLike, AiFillDislike, AiOutlineLike, AiOutlineDislike } from 'react-icons/ai';
import { UserContext } from "Context";
import EditMessage from './EditMessage';
import NewMessage from './NewMessage';
import { MdEdit } from "react-icons/md";
import { BsTrashFill } from "react-icons/bs";
import { ImQuotesRight } from "react-icons/im";

function TopicMessage({ id }) {
  const history = useHistory();
  const [user] = useContext(UserContext);
  const [messages, setMessages] = useState([]);
  const [create, setCreate] = useState(false);
  
  const [userMark, setUserMark] = useState(0);

  const [edit, setEdit] = useState(null);
  const [topic, setTopic] = useState(null);

  const getMessages = async () => {
    const array = await Message.getMessagesByTopicId(id);
    setMessages(array);
  }

  useEffect(() => {
    const getUserMark = async () => {
      const mark = await Topic.getMarkById({user_id: user.id, topic_id: topic.id});
      setUserMark(mark);
    }
    if (topic) getUserMark();
  }, [topic]);

  useEffect(() => {
    const getTopic = async () => {
      await Topic.getAll();
      setTopic(Topic.getTopicById(parseInt(id)))
    }

    getTopic();
  }, [id, userMark]);

  useEffect(() => {
    getMessages();
  }, [id, create, edit]);
  
  if (topic === undefined) {
    history.push("/forum")
    return <div className="page"></div>;
  }

  if (!topic) {
    return <div className="page"></div>;
  }

  const category = Category.getCategoryById(parseInt(topic.category_id)) || new Category();

  const DisplayMessages = () => {
    const deleteMessage = async (message, first) => {
      if (first) {
        if (!window.confirm("La suppression du message entraînera également la suppression du topic. Continuer ?")) return;
        await Topic.delete({id: message.topic_id});
        await Topic.getAll();

        history.push("/forum");
        
        return;
      }

      if (!window.confirm("Comfirmez la suppression du message.")) return;

      await Message.delete({id: message.id});
      getMessages();
    }

    return messages.map((item,index) => {
      return <MessageCard author_id={topic.user_id} message={item} key={index}>
          {(item.user_id == user.id) && <p onClick={() => setEdit(item)} className="btn">
              <MdEdit className="message-edit" />
          </p>}

          {(item.user_id == user.id || user.user_privilege === 2) && <p onClick={() => deleteMessage(item, (index == 0))} className="btn btn-red">
              <BsTrashFill className="message-edit" />
          </p>}

          {(item.user_id != user.id) && <p onClick={() => {
            window.localStorage.setItem("draft-response", "<blockquote>" + item.text_content + "</blockquote>");
            setCreate(true)
          }} className="btn btn-grey">
            <ImQuotesRight className="message-edit" />
          </p>}
      </MessageCard>
    })
  }

  const DisplayRating = () => {
    const postMark = async (mark) => {
      await Topic.postMark({
        mark: mark,
        user_id: user.id,
        topic_id: topic.id
      });
      setUserMark(mark);
    }

    return <div className="rating">
      {userMark < 0 ? <AiFillDislike onClick={() => postMark(0)} color="red" /> : 
      <AiOutlineDislike onClick={() => postMark(-1)} color="red" />}
      <p>{topic.mark}</p>
        {userMark > 0 ? <AiFillLike onClick={() => postMark(0)} color="green" /> : 
        <AiOutlineLike onClick={() => postMark(1)} color="green" />}
    </div>
  }

  if (!topic) return null;
  return <div className='topicmessage page'>
    <div className="topicmessage-head">
      <span>
        <div onClick={() => create ? setCreate(!create) : history.goBack()} className="btn btn-icon">
          <IoChevronBack />
        </div>
        <h2>{topic.title}</h2>
        
      </span>
      
      <span>
        <DisplayRating />
        {!edit && <button className="btn" onClick={() => setCreate(!create)}>
          <p>{create ? 'Retour' : "Répondre au topic"} </p>
        </button>}
        {edit && <button className="btn" onClick={() => setEdit(null)}>
          <p>Retour</p>
        </button>}
        <CategoryCard category={category} mini />
      </span>
    </div>

    {create && <NewMessage setCreate={setCreate} id={id} />}

    {edit && <EditMessage edit={edit} setEdit={setEdit} />}

    {(!create && !edit) &&
    <div className="topicmessage-overflow">
      <div className="topicmessage-wrapper">
        <DisplayMessages />
      </div>
    </div>}


  </div>
}

export default TopicMessage;
