import './DocumentPreview.scss';
import {IoMdDownload} from 'react-icons/io';
import { MdRemoveRedEye } from 'react-icons/md';
import { Document } from 'models';
import { UserContext } from "Context";
import { useContext, useEffect, useState } from "react";

import { AiFillLike, AiFillDislike, AiOutlineLike, AiOutlineDislike } from 'react-icons/ai';

function DocumentPreview({ currentDoc, setReload }) {
  const key = currentDoc?.drive_key;
  const [ user ] = useContext(UserContext);
  const [userMark, setUserMark] = useState(0);


  useEffect(() => {
    const getUserMark = async () => {
      const mark = await Document.getMarkById({user_id: user.id, document_id: currentDoc.id})
      setUserMark(mark)
    }

    if (currentDoc) getUserMark();
  }, [currentDoc, user])

  const DisplayRating = () => {

    const postMark = async (mark) => {
      await Document.postMark({
        mark: mark,
        user_id: user.id,
        document_id: currentDoc.id
      });
      setUserMark(mark);

      setReload(old => old + 1);
    }

    return <div className="rating">
      {userMark < 0 ? <AiFillDislike onClick={() => postMark(0)} color="red" /> : 
      <AiOutlineDislike onClick={() => postMark(-1)} color="red" />}
      <p>{currentDoc.mark}</p>
        {userMark > 0 ? <AiFillLike onClick={() => postMark(0)} color="green" /> : 
        <AiOutlineLike onClick={() => postMark(1)} color="green" />}
    </div>
    
  }
  return (
    <div className="document-preview">
      <div className="document-preview-content">
        {currentDoc ? 
        <iframe title="iframe" id="preview" src={"https://drive.google.com/file/d/" + key + "/preview"} allow="autoplay"/>
        : 
        <div>
          <MdRemoveRedEye />
          <h3>Cliquez sur un document pour le visualiser</h3>
        </div>}
      </div>
  
      {currentDoc ? <div className="document-preview-footer">
      <a className="btn btn-reverse" href={"https://drive.google.com/u/0/uc?id=" + key + "&export=download"}>
        <IoMdDownload /> Télecharger
      </a>

      <p>{currentDoc.title}.{currentDoc.mimetype}</p>

      <div className="document-preview-footer-star">
      <div class="stars">
          <DisplayRating />
        </div>
      </div>
      </div> : <div className="document-preview-footer">
        <p className="document-preview-footer--empty">Les informations liées aux documents appaîtront ici</p>
      </div>}
    </div>
  );
}

export default DocumentPreview;
