import { HiDotsHorizontal} from 'react-icons/hi';
import React from 'react';

import './TopicCard.scss';
import { Category, User } from 'models';
import UserIcon from 'components/UserIcon/UserIcon';
import CategoryCard from 'components/CategoryCard/CategoryCard';
import { dateToString } from "utils/date";
import { useHistory } from "react-router";

function AddTopic({ topic }) {
  const category = Category.getCategoryById(topic.category_id);
  const user = User.getUserById(topic.user_id) || new User();

  const title = topic.title.replace(" ?","").replace(/\s/g, '-')
  const url = `/forum/${topic.id}/${title}`;
  const history = useHistory();

  return (
    <div className="card topiccard" onClick={() => history.push(url)}>
      <div className="topiccard-head">
        <CategoryCard category={category} mini />

        <HiDotsHorizontal />
      </div>

      <p className="topiccard-title">{topic.title}</p>

      <div className="topiccard-content">

        <div className="topiccard-text">
          <div className="topiccard-text-name"><p>Par</p> <p className="topiccard-text-name-important">{user?.first_name}</p></div>
          <p>{dateToString(topic.creation_date)} | {topic.styleStatus.text}</p>
        </div>

        <div className="topiccard-user">
          <UserIcon size={50} user_id={topic.user_id} />
        </div>

      </div>

    </div>
  );
}

export default AddTopic;
