import { Calendar, Views, momentLocalizer } from 'react-big-calendar'
import 'react-big-calendar/lib/css/react-big-calendar.css';
import moment from 'moment';
import { CalendarEvent, Category, Class } from 'models';
import { useHistory } from "react-router-dom";

import './Timetable.scss';
import { useEffect, useState, useContext } from 'react';

import { UserContext } from 'Context';

import { useLocation } from "react-router";

require('moment/locale/fr.js')

const { moduleToCategory } = require("utils/module");

function Timetable() {
    const [listEvent, setListEvent] = useState(CalendarEvent.data);
    const localizer = momentLocalizer(moment);
    const history = useHistory();
    const [user] = useContext(UserContext);

    const location = useLocation();
    const ics = new URLSearchParams(location.search).get('ics')

    useEffect(() => {
      const getCalendarData = async () => {
        const group = user ? Class.getClassById(user?.class_id) : null;
        let link = group ? group.ics_link : undefined;
        link = ics ? ics : link;
        await CalendarEvent.getAll(link);
        setListEvent(CalendarEvent.data)
      }
  
      getCalendarData();
    }, [user, ics]);

    const list = listEvent.map(item => {
      let category_id = moduleToCategory(item.summary);
      let category = Category.getCategoryById(category_id);
      const title = (category) ? category.title : item.summary;
      return {
        title: title + " " + item.location,
        category: category,
        start: item.start,
        end: item.end
      }
    })

    return (
      <div className="page timetable">
        <Calendar
          events={list}
          localizer={localizer}
          defaultView={window.innerWidth < 800 ? Views.DAY : Views.WEEK}
          views={['week','day','agenda']}
          scrollToTime={new Date(1970, 1, 1, 6)}
          defaultDate={new Date()}
          onSelectEvent={event => {
            if (event.category) history.push(`/categories/${event.category.path}`);
          }}
          min={new Date(2017, 10, 0, 8, 0, 0)}
          max={new Date(2017, 10, 0, 19, 0, 0)} 
          eventPropGetter = {event => { 
            if (!event.category) return {};
            return { style: { background: event.category.color } }; 
          }}
          culture='fr'
        />
      </div>
    )
}


export default Timetable;