import { Message, Topic } from 'models';
import { useState, useContext } from 'react';
import TextEditor from 'components/TextEditor/TextEditor';
import { UserContext } from "Context";

function NewMessage({ setCreate, id }) {
    const [text, setText] = useState(window.localStorage.getItem("draft-response") || "");
    const [user] = useContext(UserContext);

    if (text !== window.localStorage.getItem("draft-response")) {
        window.localStorage.setItem("draft-response", text);
    }

    const sendResponse = async () => {
        await Message.post(new Message({ text_content: text, topic_id: id, user_id: user.id }))
        await Topic.getAll();
        window.localStorage.setItem("draft-response", "");
    
        setCreate(false);
    }

    return <div className="topicmessage-edit">
        <TextEditor height={window.innerHeight - 160} text={text} setText={setText} />
        <span>
        <button onClick={() => setCreate(false)} className="btn btn-grey">Retour</button>
        <button onClick={sendResponse} className="btn">Envoyer la réponse</button>
        </span>
  </div>
}

export default NewMessage;
