import React from 'react'
import { useState, useEffect } from 'react';

import "./ResetPassword.scss";
import { Link } from "react-router-dom";

const axios = require("models/axios");

const ResetPassword = () => {
    
    const [email, setEmail] = useState("");
    const [state, setState] = useState(0);

    useEffect(() => {
      document.title = "Reset Password";
    }, []);

    const resetPassword = (e) => {
        e.preventDefault();
        if(email !== ""){
            axios({
                method: "post",
                url: "api/user/resetpassword",
                data: { email: email}
              }).then((response) => {
                if(response.data.err){
                  console.log(response.data.err);
                  setState(2);
                } else {
                  setState(1);
                }
              })
        }
    }

    return (
      <div className="centerpage">
     
        <div className="centerpage-main card">
          {state === 0 &&
            <form onSubmit={resetPassword}>
              <h2>Entrez votre addresse mail</h2>
              <input required type='email' placeholder="email" value={email} onChange={e => setEmail(e.target.value)}></input>

              <button className="btn" type="submit">Confirmer</button>
              <Link className="btn btn-grey" to="/login">Retour à la page de connexion</Link>
            </form>
          }

          {state === 1 && <>
            <h2>Un mail vous a été envoyé.</h2>

            <Link className="btn" to="/login">Retour à la page de connexion</Link>
            </>
          }

          {state === 2 && <>
            <h2 style={{color: "red"}}>Cet email n'est pas présent dans notre base de données.</h2>

            <Link className="btn" to="/login">Retour à la page de connexion</Link>
            </>
          }

        </div>
      </div>
    )
}

export default ResetPassword
