import './TextEditor.scss';
import Editor from 'ckeditor5-custom-build/ckeditor';
import { CKEditor } from '@ckeditor/ckeditor5-react'

const editorConfiguration = {

  toolbar: {
    items: [
      'heading',
      '|',
      'bold',
      'underline',
      'italic',
      'fontColor',
      // 'fontSize',
      // 'fontFamily',
      // 'fontBackgroundColor',
      'highlight',
      // 'specialCharacters',
      // '|',
      'bulletedList',
      // 'numberedList',
      // 'insertTable',
      '|',
      'undo',
      'redo',
      '|',
      'imageUpload',
      'blockQuote',
      'codeBlock'
    ]
  },
  language: 'fr',

  table: {
    contentToolbar: [
      'tableColumn',
      'tableRow',
      'mergeTableCells',
      'tableProperties'
    ]
  },


  placeholder: '',
  removePlugins: ['Title'],
  codeBlock : {
    languages: [
    { language: 'plaintext', label: 'Text' }, // The default language.
    
    // { language: 'asm6502', label: 'Assembleur' },
    { language: 'batch', label: 'Batch' },
    { language: 'c', label: 'C' },
    { language: 'cs', label: 'C#' },
    { language: 'cpp', label: 'C++' },
    { language: 'css', label: 'CSS' },
    { language: 'html', label: 'HTML' },
    { language: 'java', label: 'Java' },
    { language: 'javascript', label: 'JavaScript' },
    { language: 'sql', label: 'SQL' },
    { language: 'markdown', label: 'Markdown' },
    { language: 'php', label: 'PHP' },
    { language: 'python', label: 'Python' },
  ],

  wordCount: {
    onUpdate: stats => {
        // Prints the current content statistics.
        console.log( `Characters: ${ stats.characters }\nWords: ${ stats.words }` );
    }
  }
  }
};

function TextEditor({ text, setText, height }) {

  return <div>
    <CKEditor
      editor={ Editor }
      data={text}
      onReady={ editor => {
        if (!editor) return;
        editor.editing.view.change((writer) => {
          writer.setStyle(
            "height",
            height ? height - 45 + "px" : "500px",
            editor.editing.view.document.getRoot()
          );
        });

        console.log(document.getElementById( 'word-count' ));
        }}
        onChange={ (event, editor ) => {
          let content = editor.getData();
          console.log(content.length)
          if(content.length < 500_000) setText(content);
          else {
            editor.setData(text);
            alert("Erreur: L'image proposée est trop volumineuse");
          }
        }}
        config={editorConfiguration}
     />
  </div>

}



export default TextEditor;
