import fetchData from 'utils/fetchData';

class CalendarEvent {

    static data = [];

    constructor(opt) {
        if (!opt) opt = {};

        this.start = new Date(opt.start);
        this.end = new Date(opt.end);
        this.summary = opt.summary || "";
        this.location = opt.location ? opt.location.replace("\\,", "/") : "";
        this.description = opt.description || "";
    }

    static getAll = async (link) => {
        const data = await fetchData({ url: "api/calendar/ics/" + link });
        CalendarEvent.data = data.map(element => new CalendarEvent(element));
    }
}

export default CalendarEvent;