import fetchData from "utils/fetchData";
import Category from "./Category";
const { moduleToCategory } = require("utils/module");


class Mark {

    static data = [];

    // constructor
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id || Mark.id;
        this.user_id = opt.user_id;
        this.title = opt.title || "";
        this.creation_date = opt.creation_date || new Date();
        this.tomuss = opt.tomuss || 0;
        this.deleted = opt.deleted || 0;
        this.max = parseFloat(opt.max) || 20;
        this.grade = parseFloat(opt.grade) || 10;
        this.coef = opt.coef || 1;
        this.description = opt.description || "";

        this.category_id = moduleToCategory(this.description);
    }

    static averageMark = (array) => {
        let res = parseFloat(
            array.reduce((acc, elt) => acc + elt.grade * elt.coef, 0) / 
            array.reduce((acc, elt) => acc + elt.coef, 0));
        return res.toFixed(2);
    }

    static averageModule = (semester, display) => {
        let array = Mark.data.filter(item => Category.getCategoryById(item.category_id)?.category_id == semester);
        let tab = [];
        
        array.forEach(element => {
            const m = tab.find(item => item.category_id == element.category_id);
            if (m) m.grades.push({grade: element.grade, coef: element.coef })
            else tab.push({ category_id: element.category_id, grades: [{grade: element.grade, coef: element.coef }] })
        });

        tab = tab.map(item => {
            return {
              category_id: item.category_id,
              coef: Category.getCategoryById(item.category_id)?.coef,
              grade: parseFloat((
                item.grades.reduce((acc, elt) => acc + elt.grade * elt.coef, 0) /
                item.grades.reduce((acc, elt) => acc + elt.coef, 0)).toFixed(2))
            }
        })

        if (display) {
            const category_semester = Category.data.filter(item => item.category_id == semester);
            for (const category of category_semester) {
                const union = tab.find(item => item.category_id == category.id);
                if (!union) {
                    tab.push({
                        category_id: category.id,
                        coef: category.coef,
                        grade: 0
                    })
                }
            }
        }

        return tab;
    }

    // getters
    static getMarkById = (id) => {
        return Mark.data.find(x => x.id === id);
    }

    static post = async (obj) => {
        const mark = new Mark(obj);
        const res = await fetchData({
            url: "api/mark",
            method: 'POST',
            data: mark
        })

        mark.id = res[0]?.insertId;
        if (mark.id) Mark.data.push(mark);
    }

    static update = async (mark) => {
        await fetchData({
            url: "api/mark",
            method: 'PUT',
            data: new Mark(mark)
        })

        Mark.data = [...Mark.data].filter(item => item.id != mark.id);

        Mark.data.push(new Mark(mark));
    }

    static delete = async (id) => {
        await fetchData({
            url: "api/mark",
            method: 'DELETE',
            data: {
                id: id
            }
        })

        Mark.data = Mark.data.filter(item => item.id != id);
    }

    static getAll = async (id) => {
        const data = await fetchData({ url: "api/mark/user/" + id });
        Mark.data = data.map(element => new Mark(element));
        console.log(data)
        return Mark.data;
    }

}

export default Mark;