import React from 'react';
import { useHistory } from 'react-router';
import { useState, useEffect, useContext } from 'react';
import { useLocation } from 'react-router-dom';
import { AiFillStar, AiOutlineStar } from 'react-icons/ai';

import CategoryCard from 'components/CategoryCard/CategoryCard';
import DocumentSection from 'pages/DocumentSection/DocumentSection';
import { Category, Class, Favorite, Mark } from 'models';
import './Categories.scss';
import { UserContext } from "Context";
import Aside from 'components/Aside/Aside';
import MiniCalendar from 'components/MiniCalendar/MiniCalendar';
import CardMark from 'components/CardMark/CardMark';

function Categories() {
  const location = useLocation();
  const history = useHistory();
  const [category, setCategory] = useState(null);
  const [favoris, setFavoris] = useState(false);
  const [user] = useContext(UserContext);
  const [marks, setMarks] = useState(Mark.data);
  const [minVote, setMinVote] = useState(-2);

  /* -------- */

  // we update the component each time the url changes
  useEffect(() => {
    if (category) { setCategory(null); }
  }, [location]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    const fetchData = async () => {
      const tab_marks = await Mark.getAll();
      setMarks(tab_marks);
    }
    
    if (!Mark.data.length) fetchData();
  }, []);

  // we define the category according to the url
  if (category === null) { 
    let pathname = window.location.pathname;
    setCategory(
      Category.getCategoryByPath(pathname.replace(`/${pathname.split("/")[1]}/`,''))
    );

    return null;
  }

  // if the url does not correspond to any category, we redirect to the IT department
  if (category === undefined) { 
    const path_parent = window.location.pathname.split("/")[1];

    const user_department = Class.getClassById(user?.class_id)?.department_id;
    const main_category = Category.getCategoryById(user_department);
    const path_default = main_category?.title.replace(/\s/g, '-');
    
    if (main_category) history.push(`/${path_parent}/${path_default}`);

    return null;
  }
  
  // we check if the category is in the user's favorites
  const fav = Favorite.isFavorite(user?.id, category.id);
  if (fav && !favoris) setFavoris(true);
  if (!fav && favoris) setFavoris(false);

  /* -------- */

  const DisplayCategories = () => {
    if (category === undefined) return null 
    const list = Category.getCategoryChild(category.id);

    return list.map((item,index) => {
      return <CategoryCard category={item} key={index} />
    })
  }

  const DisplayPath = () => {
    if (category === undefined) return null 
    return category.tabPath.map((item,index)=>{
      return <CategoryCard key={index} category={item} mini />
    })
  }

  const changeFavoris = async () => {
    if (favoris) {
      await Favorite.delete(user.id, category.id);
      setFavoris(false);
    } else {
      await Favorite.post(user.id, category.id);
      setFavoris(true);
    }
  }



  const mark = marks.find(item => item.category_id == category.id)


  return (
    <>
    <div className="page categories">

      <div className="categories-head">
        <div className="categories-head-path">
          <DisplayPath />
        </div>

        <div onClick={changeFavoris}>
          {favoris ? <AiFillStar /> : <AiOutlineStar />}
        </div>
      </div>

      
      {category && !category.hasChild ? <div className="categories-section">
        <DocumentSection minVote={minVote} category={category} />
      </div>
      : <div className="categories-navigation">
        <DisplayCategories />
      </div>
      }
    </div>
    
    {category && !category.hasChild ? <Aside>
      <div className="categories-aside">
        {/*<MiniCalendar category_id={category.id} />*/}

        <div className='categories-vote'>
          <label>Nombre de vote minimum des documents</label>
          <span>
            <input value={minVote} onChange={e => setMinVote(e.target.value)} type="number" min="-10" max="10" />
            <input value={minVote} onChange={e => setMinVote(e.target.value)} type="range" id="volume" name="volume" min="-10" max="10" />
          </span>
        </div>

        {mark && <div className="categories-aside-mark">
          <CardMark mark={mark} />
        </div>}

        <button onClick={() => history.push(`/forum?category=${category.id}&semester=${category.category_id}`)} className="btn">
          Accéder au forum de cette categorie
        </button>
        
      </div>
    </Aside> : <Aside />}
    </>
  );
}

export default Categories;