import fetchData from 'utils/fetchData';

class Category {
    static data = [];

    /* ---- constructor ---- */
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id;

        this.category_id = opt.category_id || 0;
        this.color_id = opt.color_id || 2;

        this.title = opt.title || "unknown";
        this.icon = opt.icon || "https://img.icons8.com/metro/260/000000/school.png";
        this.module = opt.module || "????";
        this.color = opt.color || "#ededed";
        this.coef = opt.coef || 1;
    }

    /* ---- object methods ---- */

    get hasChild () {
        return Category.data.find(x => x.category_id === this.id) !== undefined;
    }

    get path() {
        let full_path = this.title;
        let current = this;

        for (let i=0; i<Category.data.length; i++) {
            current = Category.data.find(x => x.id === current.category_id);
            if (current === undefined) break;
            full_path = `${current.title}/${full_path}`;
        }

        return full_path.replace(/\s/g, '-');
    }

    get background() {
        // affichage pour les thèmes sombres
        if (window.getComputedStyle(document.documentElement).getPropertyValue("--white") !== "white") {
            return `linear-gradient(30deg, ${this.color} 0%, ${this.color} 100%)`;
        }

        // affichage normal
        return `linear-gradient(30deg, ${this.color} 40%, ${this.color}9b 100%)`;
    }

    get tabPath() {
        let tab = [this];
        let current = this;

        for (let i=0; i<Category.data.length; i++) {
            current = Category.data.find(x => x.id === current.category_id);
            if (current === undefined) break;
            tab.push(current);
        }

        return tab.reverse();
    }

    /* ---- statics methods ---- */

    /* CRUD methods */
    static createCategory = (object) => {
        return false;
    }

    static deleteCategory = (id) => {
        return false;
    }

    static updateCategory = (object) => {
        return false;
    }

    /* getters */
    static getCategoryById = (id) => {
        return Category.data.find(x => x.id === id);
    }

    static getCategoryByPath = (str) => {
        return Category.data.find(x => encodeURI(x.path) === str);
    }

    static getCategoryByModule = (str) => {
        let c = Category.data.find(item => {
            let operation = false;
            let module = item.module;
            if (str.includes(module)) operation = true;
            return operation;
        });

        return c;
    }

    static getCategoryChild = (id) => {
        return Category.data.filter(x => x.category_id === id);
    }

    static getAll = async () => {
        const data = await fetchData({ url: "api/category" });
        Category.data = data.map(element => new Category(element));
    }

}

export default Category;