export const setThemes = (opt) => {
    if (!opt) opt = {};
  
    document.documentElement.style.setProperty("--main", opt.main || "#4476DE");
    document.documentElement.style.setProperty("--back", opt.back || "#E7F1FB");
    document.documentElement.style.setProperty("--back2", opt.back2 || "#F9FCFF");
  
    document.documentElement.style.setProperty("--grey", opt.grey || "#D1D9E5");
    document.documentElement.style.setProperty("--black", opt.black || "black");
    document.documentElement.style.setProperty("--white", opt.white || "#FFFFFFd2");  
}
