import { Message } from 'models';
import { useState } from 'react';
import TextEditor from 'components/TextEditor/TextEditor';

function EditMessage({ edit, setEdit }) {
    const [textEdit, setTextEdit] = useState(edit.text_content);

    const modifMessage = async () => {
        await Message.update({id: edit.id, text_content: textEdit});
        setEdit(null);
    }

    return <div className="topicmessage-edit">
        <TextEditor height={window.innerHeight - 160} text={textEdit} setText={setTextEdit} />
        <span>
            <button onClick={() => setEdit(null)} className="btn btn-grey">Retour</button>
            <button onClick={modifMessage} className="btn">Modifier la réponse</button>
        </span>
    </div>
}

export default EditMessage;
