import fetchData from 'utils/fetchData';

class Conversation {

    static data = [];

    // constructor
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id;
        this.user_id = opt.user_id;
        this.last_message = new Date(opt.last_message).toISOString() || new Date(1970).toISOString();
    }

    static post = async (topic) => {
        const response = await fetchData({
            method: 'post',
            url: 'api/conversation',
            data: topic
        })

        return response;
    }


    static update = (object) => {
        return false;
    }

    static delete = async (id) => {
        await fetchData({ url: 'api/topic/' + id, method: 'delete' });
    }

    // getters
    static getAllByUserID = async (user_id) => {
        let data = await fetchData({ url: `api/conversation/${user_id}` });
        return data.map(item => {
            return new Conversation({
                id: item.id,
                user_id: (item.user1_id == user_id) ? item.user2_id : item.user1_id,
                last_message: item.last_message
            })
        })

    }
}

export default Conversation;