import { UserContext, NotifContext } from 'Context';
import React from 'react'
import axios from 'models/axios';
import { User, Message, Conversation } from 'models';
import "./Conversations.scss";
import { useHistory, useParams } from 'react-router';

import { useContext, useEffect, useState } from 'react';

import ConvCard from './ConvCard/ConvCard.js'
import { AiOutlinePlus } from "react-icons/ai";

const Conversations = ({ reload }) => {
    const { id } = useParams();
    const allUsers = User.data;
    const [user] = useContext(UserContext);
    const [notif, setNotif] = useContext(NotifContext);
    const [newConvSelectVal, setNewConvSelectVal] = useState(null);
    const history = useHistory();

    const getConversations = async () => {
        let result = await Conversation.getAllByUserID(user.id);
        if (JSON.stringify(result) !== JSON.stringify(notif.conv)) {
            setNotif(prev => ({ ...prev, conv: result}));
        }
    }

    useEffect(() => {
        getConversations();
    }, [user, reload, newConvSelectVal])

    const newConversation = async (e) => {
        e.preventDefault();

        const conv_id = await Conversation.post({ user1_id: user.id, user2_id: parseInt(newConvSelectVal)})  
        history.push("/private-messages/" + conv_id)
        getConversations();
        setNewConvSelectVal("");
    }

    const DisplayOption = () => {
        const conv = notif.conv || [];
        const tab = allUsers.filter(item => !conv.find(element => element.user_id == item.id));
        
        return tab.map(user => <option value={user.id}>{user.first_name} {user.last_name}</option>)
    }

    const DisplayConv = () => {
        const conv = notif.conv ? [...notif.conv] : [];

        conv.sort((b,a) => new Date(a.last_message) - new Date(b.last_message));

        return conv.map(item => {
            let state = true;
            if (notif.dateConv) {
                state = !(notif.dateConv.find(element => element.id == item.id && new Date(item.last_message) <= new Date(element.date)));
            }
            
            return <ConvCard notif={state} active={(item.id == parseInt(id))} conversation={item}/>
        });
    }

    return (
        <div className="conversations">

            <form onSubmit={newConversation}>
                <select value={newConvSelectVal} onChange={(e) => setNewConvSelectVal(e.target.value)} name="user-select" id="user-select">
                <option value="">-- utilisateurs --</option>
                    <DisplayOption />
                </select>
             
                <button className="btn btn-circle" disabled={!(newConvSelectVal)}><AiOutlinePlus /></button>
            </form>

            <div className="wrapper">
                <DisplayConv />
            </div>

           

        </div>
    )
}

export default Conversations
