import fetchData from "utils/fetchData";

class Color {

    static data = [];

    // constructor
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id || 0;
        this.title = opt.title || "";
        this.color = opt.color || "";
    }

    static getAll = async () => {
        const data = await fetchData({ url: "api/color" });
        Color.data = data.map(element => new Color(element));
    }

}

export default Color;