import { RiQuillPenFill } from 'react-icons/ri';
import { Link } from 'react-router-dom';
import React from 'react';

import icon_question from 'assets/icon_question.png';
import './AddTopic.scss';

function AddTopic() {

  const sentences = [
    "Vous bloquez sur un excercice?\nPosez votre question!",
    "Vous avez du mal avec une notion de cours?\nPosez votre question!",
  ]

  return (
    <div className="card addtopic">
      <img alt="icon-question" src={icon_question} />

      <div className="addtopic-text">
        <div>
          <h3>Ajouter un topic</h3>
          <p>{sentences[Math.floor(Math.random() * sentences.length)]}</p>
        </div>

        <Link to="/createtopic" className="btn">
          <RiQuillPenFill />
          <p>Allons-y !</p>
        </Link>
      </div>
      
    </div>
  );
}

export default AddTopic;
