import './Register.scss';

import {useState} from 'react';
import RegisterMail from "./RegisterMail/RegisterMail";
import RegisterInfo from './RegisterInfo/RegisterInfo';
import RegisterClass from './RegisterClass/RegisterClass';
import RegisterFavorite from './RegisterFavorite/RegisterFavorite';
import RegisterPicture from './RegisterPicture/RegisterPicture';
import { Link } from 'react-router-dom';
import { Favorite, Class } from 'models';
import { useEffect } from 'react';

const axios = require("models/axios");

function Register() {

  useEffect(() => {
    document.title = "S'inscrire";
  }, []);

  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);

  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [classe, setClasse] = useState(0);
  const [favorite, setFavorite] = useState([]);

  const [icon, setIcon] = useState("");

  const createUser = (e) => {

    e.preventDefault();
      setLoading(true);

      if (!icon || icon === "") {
        postUser("https://img.icons8.com/officel/1600/ffffff/user.png");
        return;
      }

      const fileData = DataURIToBlob(icon);
      const formData = new FormData();
      formData.append('file', fileData);
      formData.append('parent', "1EVQnu9iquLwkIDuBYksDNxQF3cywmrfG");
      formData.append("file_name", firstName+"-"+lastName);

      axios.post('/api/drive', formData, { 
        headers: { 'Content-Type': 'multipart/form-data' },
      })
      .then((response) => {
        if(response.data.err){
          console.log("Erreur lors de l'envoi de votre photo de profil sur la base de données.");
          setLoading(false);
        } else {
          postUser("https://drive.google.com/uc?export=view&id=" + response.data);
        }
      })
  }

  const postUser = (urlIcon) => {
    const newUser = {
      first_name: firstName,
      last_name: lastName,
      crypted_password: password,
      mail: email + "@etu.univ-lyon1.fr",
      icon: urlIcon,
      class_id: parseInt(classe),
      favorite: favorite
    }

    axios({
      method: 'post',
      url: 'api/user',
      data: newUser
    })
    .then((response) => {
      if(response.data.err){
        console.log(response.data.err);
      } else {
        Favorite.getAll();
        setStep(step + 1);
      }
    })
    .then(() => setLoading(false))
  }

  const LastStep = () => {
    return <form>
      <h2>Un mail t'a été envoyé pour confirmer ton inscription</h2>

      <a className="btn" target="_blank" rel="noopener noreferrer" href="https://mail.univ-lyon1.fr/">Accéder à ma boite mail</a>

      <Link className="btn btn-gold" to="/login">Me connecter</Link>
    </form>
  }

  const SendUser = () => {
    const c = Class.getClassById(parseInt(classe));
    
    const grp = c ? c.title : "";

    return <form>
          <h2>Valider vos informations</h2>

          <div className="register-recap">
            <img src={(!icon || icon === "") ? "https://img.icons8.com/officel/1600/ffffff/user.png" : icon} alt="user icon" />
            
            <p>{firstName} {lastName} - {grp}</p>
            <p className="register-recap-mail">{email}@etu.univ-lyon1.fr</p>

            <div>
              {loading && <p>Votre compte est en cours de création..</p>}
            </div>
          </div>

          <div className="register-button">
            <div className="btn btn-grey" onClick={() => setStep(step - 1)}>Précédent</div>
            <button className="btn" onClick={createUser} disabled={loading}>Créer mon compte</button>
          </div>
    </form>
  }

  const steps = [
    { title: "Adresse E-Mail", component: RegisterMail},
    { title: "Informations Persos", component: RegisterInfo},
    { title: "Classe", component: RegisterClass},
    { title: "Matières favorites", component: RegisterFavorite},
    { title: "Photo de profil", component: RegisterPicture},
    { component: SendUser },
    { component: LastStep }
  ]

  const DisplaySteps = () => {
    return steps.map((item, index) => {
      if (!item.title) return null;
      const i = index + 1;
      return  <div key={index}>      
        {index !== 0 && <div className={step >= i ? "line line-active" : "line"}></div>}
        <span>
          <div className={step >= i ? "cirle active" : "cirle"}>{i}</div>
          <p className={step >= i ? "p-active" : ""}>{item.title}</p>
        </span>
    </div>
    })
  }

  return (
    <div className="page register">

      <div className="register-main">

      <div className="register-figure">
        <DisplaySteps />
      </div>

      {step === 1 && <RegisterMail 
        setEmail={setEmail}
        email={email}
        setFirstName={setFirstName}
        setLastName={setLastName}
        setStep={setStep}
        step={step}
      />}

      {step === 2 && <RegisterInfo
        firstName={firstName}
        setFirstName={setFirstName}
        lastName={lastName}
        setLastName={setLastName}
        password={password}
        setPassword={setPassword}
        setStep={setStep}
        step={step}
      />}

      {step === 3 && <RegisterClass
        setStep={setStep}
        step={step}
        classe={classe}
        setClasse={setClasse}
        setFavorite={setFavorite}
      />}

      {step === 4 && <RegisterFavorite
        setStep={setStep}
        step={step}
        favorite={favorite}
        setFavorite={setFavorite}
        classe={classe}
      />}

      {step === 5 && <RegisterPicture
        setStep={setStep}
        step={step}
        icon={icon}
        setIcon={setIcon}
      />}

      {step === 6 && <SendUser />}

      {step === 7 && <LastStep />}
      </div>
    </div>
  );
}

function DataURIToBlob(dataURI) {
  const splitDataURI = dataURI.split(',')
  const byteString = splitDataURI[0].indexOf('base64') >= 0 ? atob(splitDataURI[1]) : decodeURI(splitDataURI[1])
  const mimeString = splitDataURI[0].split(':')[1].split(';')[0]

  const ia = new Uint8Array(byteString.length)
  for (let i = 0; i < byteString.length; i++)
      ia[i] = byteString.charCodeAt(i)

  return new Blob([ia], { type: mimeString })
}

export default Register;