import {BrowserRouter as Router, Route } from 'react-router-dom';
import React, {useState, useEffect, useContext } from 'react';
import ReactDOM from 'react-dom';
import { Redirect, Switch } from 'react-router';

import io from 'socket.io-client';

import 'index.scss';
import Header from 'components/Header/Header';
import ResetPassword from 'pages/ResetPassword/ResetPassword';
import Aside from 'components/Aside/Aside';
import { User, Category, Topic, Document, Class, Favorite, Mark, Color } from "models";
import Register from 'pages/Register/Register';
import Login from 'pages/Login/Login';
import Verification from 'pages/Verification/Verification.js';
import ResetPasswordOn from 'pages/ResetPassword/ResetPasswordOn';
import { UserContext, TreeContext, NotifContext, UserProvider, TreeProvider, NotifProvider } from "./Context";
import { themes, setThemes } from "utils/themes";

import auth from 'utils/auth';
import Construction from 'components/Construction/Construction';

function App() {

  let socket;

  const [load, setLoad] = useState(false);
  const [user, setUser] = useContext(UserContext);
  const [tree] = useContext(TreeContext);

  /* if the login informations are present in the local storage, we automatically try to connect the user */
  async function login() {
    const mail = window.localStorage.getItem("mail");
    const password = window.localStorage.getItem("password");

    if (mail && password) {
      const result = await auth(mail, password);
      setUser(result);
    }

    setLoad(true);
  }

  /* launches the loading of data for all the classes */
  const fetchData = async () => {
    await User.getAll(); 
    await Category.getAll();
    await Topic.getAll();
    await Document.getAll();
    await Class.getAll();
    await Favorite.getAll(); 
    await Color.getAll();
    login();
  }
  

  useEffect(() => {
    if (window.localStorage.getItem("theme")) setThemes(themes[window.localStorage.getItem("theme")]);
    else setThemes(themes[0]);

    fetchData();
  }, [])

  useEffect(() => {

    if (user && !user.socket) {
      socket = io("https://aled-api.herokuapp.com/api");
      socket.emit("join-user", user.id);
      setUser({
        ...user,
        socket: socket
      })
    }

  }, [user])

  /* create all the routes of the application according to the variable tree */
  const BuildRoute = () => {
    return tree?.map((page, index) => {      
      return <Route key={index} exact={(page.exact)} path={page.path}>

        <page.component />

        {!page.customAside && <Aside />}
      </Route>
    })
  }

  const DisplayNormal = () => {
    return <Router>

        <Header />

        <BuildRoute />
        
    </Router>
  }

  const DisplayRestricted = () => {
    return <Router>
      <Switch>
        <Route exact path={"/"}>
          <Login />
        </Route>

        <Route exact path="/verification/:id">
          <Verification />
        </Route>

        <Route exact path="/resetpassword">
          <ResetPassword />
        </Route>

        <Route exact path="/resetpassword/:id">
          <ResetPasswordOn />
        </Route>

        <Route exact path={"/register"}>
          <Register />
        </Route>

        <Redirect from='*' to='/' />
      </Switch>
    </Router>
  }
  
  return (
    <div className="app">
        {load === true ? user ? <DisplayNormal /> : <DisplayRestricted /> : <div className="loading">
            <div className="loadingOuter">
            <div className="loadingInner"></div>
          </div>
        </div>
        }
        <div className='limit'><Construction /></div>
        
    </div>
  );
}



ReactDOM.render(
  <React.StrictMode>
    <NotifProvider><TreeProvider><UserProvider>
      <App />
    </UserProvider></TreeProvider></NotifProvider>
  </React.StrictMode>,
  document.getElementById('root')
);