import './RegisterMail.scss';
import { Link } from 'react-router-dom';
const axios = require("models/axios");

function RegisterMail({ setStep, step, setFirstName, setLastName, setEmail, email }) {
  
  const confirm = (e) => {
    e.preventDefault();

    axios({
      method: 'post',
      url: 'api/user/checkemail',
      data: {
        mail: email + "@etu.univ-lyon1.fr",
      }
    })
    .then((response) => {
      if(response.data.err){
        alert(response.data.err);
      } else {
        nextStep();
      }
    })
  }

  const nextStep = () => {
    const fullname = email.split(".");
    if (fullname[0]) setFirstName(fullname[0]);
    if (fullname[1]) setLastName(fullname[1]);

    setStep(step + 1);
  }

  return <form onSubmit={confirm}>
      <h2>Renseigner mes informations de connexion</h2>

      <div className="register-input">
        <label htmlFor="username">Entrez le début de votre adresse mail universitaire</label>
        <div className="login-arobase">
          <input 
            autoFocus 
            pattern="*\.*" 
            title="Celui-ci doit être sous la forme: [prenom].[nom]"
            value={email} 
            onChange={(e) => setEmail(e.target.value) } 
            type="text" 
            id="username" 
            placeholder="prenom.nom"
            required
            autoComplete="username"
          />
          <p>@etu.univ-lyon1.fr</p>
        </div>
      </div>

      <div className="register-button">
        <Link className="btn btn-grey" to="/login">J'ai déjà un compte !</Link>
        <button className="btn" type="submit">Suivant</button>
      </div>
  </form>

}

export default RegisterMail;
