import './StatsMark.scss';
import { Bar } from "react-chartjs-2";
import { Category, Mark } from 'models';

function StatsMark({ semester, setCategory, displayEmpty }) {
  
    let tab = Mark.averageModule(semester, displayEmpty);

    const data_grade = tab.map(item => item.grade);
    const data_color = tab.map(item => Category.getCategoryById(item.category_id).color);
    const data_label = tab.map(item => Category.getCategoryById(item.category_id).title);
    
    const data = {
      labels: data_label,
      datasets: [
          {
            label: 'Moyenne par matière',
            data: data_grade,
            backgroundColor: data_color,
            borderWidth: 1
          }
      ],
    }


  return <div className="stats">
    <div className="bar">
      <Bar
        data={data}
        title="Moyenne par matière"
        color="#70CAD1"
        options={{
          scales : {
            y : {
                min: 0,
                max: 20,
            }},
            onClick: (e, element) => {
              if (!element[0]) return;

              const index = element[0].index;
              setCategory(tab[index]?.category_id)
            }
        }}
      />
    </div>
  </div>

}

export default StatsMark;
