import './TimetableCard.scss';
import { Link } from 'react-router-dom';
import { GiTeacher } from 'react-icons/gi';
import { TiGroup } from 'react-icons/ti';
import { FaDoorOpen } from 'react-icons/fa';

const TimetableCard = ({ timetable }) => {
  let Icon = () => <GiTeacher />
  const type = timetable.type;
  let info = "";

  if (type == "ROOM_INFO" || type == "ROOM_TD" || type == "ROOM_OTHER") {
    Icon = () => <FaDoorOpen />;
  }

  if (type == "GROUP") {
    Icon = () => <TiGroup />
  }

  if (type == "GROUP") info = "groupe";
  if (type == "PROF") info = "professeur";
  if (type == "ROOM_INFO") info = "salle informatique";
  if (type == "ROOM_TD") info = "salle de td";
  if (type == "ROOM_OTHER") info = "autre salles";

  return <Link to={"/calendar/user?ics=" + timetable.link} className="timetablecard card">
    <h2>{timetable.title}</h2>
    <Icon />
    <p>{info}</p>
  </Link>
}


export default TimetableCard;
