import "./Edit.scss";

import { Class, Category, User } from "models";

import { useContext, useState, useEffect } from "react";
import { UserContext } from "Context";
import { useHistory } from "react-router";

import React from 'react'

const Edit = () => {

  
   
   
    const history = useHistory();
    const [user] = useContext(UserContext);

    //States


    const [ok, setOk] = useState(false);
    const [oldMdp, setOldMdp] = useState("");
    const [newMdp, setNewMdp] = useState("");
    const [flux, setFlux] = useState(user.tomuss)
    const classes = Class.data;
    const [groupSelect, setGroupSelect] = useState(user.class_id.toString());


    useEffect(() => {


        if((groupSelect.length > 0 && groupSelect != user.class_id) || flux != user.tomuss || (newMdp.length >= 8 && oldMdp.length >= 8) ){
            
            setOk(true);

            

            
        }else{
            setOk(false);
        }

    }, [oldMdp, newMdp, flux, groupSelect])

    const confirmEdit = (e) => {
        e.preventDefault();
        const data = {
            class_id: parseInt(groupSelect) != user.class_id ? parseInt(groupSelect) : user.class_id,
            password: newMdp.length > 0 ? newMdp : "",
            oldPassword: oldMdp.length > 0 ? oldMdp : "",
            tomuss: (flux != user.tomuss) ? flux : user.tomuss
        }
        console.log(data);
        User.editById(data, user.id);
    }

    const DisplayOptionGroup = () => {
        return classes.map(c => <option value={c.id}>{c.title + "S" + Category.getCategoryById(c.semester_id).title.substr(Category.getCategoryById(c.semester_id).title.length - 1, 1)}</option>)
        
        //return classes.map(user => <option value={user.id}>class.title{user.first_name} {user.last_name}</option>)
    }

    const DisplayOption = () => {
        return <option value={"ok"}>OK</option>;
    }

    return (
        <div className="edit-profile page">
            <div>
                <h1>Modifier mon profil</h1>

                <form onSubmit={confirmEdit}>
                    <div className="form-inputs">
                        <div>
                            <h3>Ma photo :</h3>
                            <input type="file" placeholder=""></input>
                        </div>
                        <div>
                            <h3>Mon flux tomuss :</h3>
                            <input type="text" placeholder="Flux Tomuss" value={flux} onChange={(e) => setFlux(e.target.value)}></input>
                        </div>
                        <div>
                            <h3>Ma classe :</h3>
                            <select value={groupSelect} onChange={(e) => setGroupSelect(e.target.value)} name="group-select" id="group-select">
                                <option value="">Choix</option>
                                    <DisplayOptionGroup />
                            </select>
                        </div>
                        <div>
                            <h3>Mon mot de passe :</h3>
                            <input type="password" placeholder="Ancien mot de passe" value={oldMdp} onChange={(e) => setOldMdp(e.target.value)}></input>
                            <input type="password" placeholder="Nouveau mot de passe" value={newMdp} onChange={(e) => setNewMdp(e.target.value)}></input>
                        </div>
                    </div>
                    <div className="form-btn">
                        <button className={"btn btn-lightgrey"}>Annuler</button>
                        <button className="btn"  disabled={ok ? false : true} type="submit">Confimer</button>
                    </div>
                </form>

            </div>
        </div>
    )
}

export default Edit
