import { useState, useContext } from 'react';
import TextEditor from 'components/TextEditor/TextEditor';
import MessageCard from 'components/Message/Message';
import './CreateTopic.scss';
import { Message, Category, Class, Topic } from 'models';
import { UserContext } from "Context";
import { useHistory, useLocation } from 'react-router';

function CreateTopic() {
  const location = useLocation();
  const history = useHistory()
  const searchParams = new URLSearchParams(location.search);
  const semester_value = searchParams.get("semester") || "";
  const category_value = searchParams.get("category") || "";

  const [text, setText] = useState(window.localStorage.getItem("draft") ? window.localStorage.getItem("draft") : "");
  const [displayPreview, setDisplayPreview] = useState(false);
  const [user] = useContext(UserContext);

  const onChange = (e) => {
    const { name, value } = e.target;
    if (!value || value === "") {
      searchParams.delete(name);
    } else {
      searchParams.set(name, value);
    }
    
    history.push("/createtopic?" + searchParams.toString());
  }

  const SetCategory = () => {
    const BuildSemester = () => {
      const semesters = Category.data.filter((item) => item.category_id === Class.getClassById(user.class_id).department_id);

      return semesters.map((item,index) => <option key={index} value={item.id}>{item.title}</option>)
    }

    const BuildCategory = () => {
      if (semester_value === "") return null;
      const categories = Category.data.filter((item) => item.category_id === parseInt(semester_value));

      return categories.map((item,index) => <option key={index} value={item.id}>{item.title}</option>)
    }

    return <>
      <select 
        value={semester_value} 
        name="semester" 
        onChange={e => { 
          searchParams.delete("category");
          history.push("/forum?" + searchParams.toString());
          onChange(e);
        }} 
        id="search-semester"
        required>
          <option value="">- semestre -</option>
          <BuildSemester />
      </select>
      <select disabled={semester_value === ""} value={category_value} name="category" onChange={onChange} required id="search-category">
          <option value="">- catégorie -</option>
          <BuildCategory />
      </select>
    </>
  }

  window.localStorage.setItem("draft",text);

  const postTopic = async (e) => {
    e.preventDefault();

    const topic = new Topic({ 
      user_id: user.id,  
      category_id: parseInt(document.getElementById('search-category').value),
      title: document.getElementById('topic-title').value,
    });

    const id_topic = await Topic.post(topic);

    if (!id_topic) {
      alert("La création du topic a échouée.");
      return;
    }

    await Topic.getAll();

    const message = new Message({ 
      user_id: user.id,  
      topic_id: id_topic,
      text_content: text
    });

    await Message.post(message);

    history.push("/forum/" + id_topic)
  }
  
  return (
    <form onSubmit={postTopic} className="page createtopic">
      <div className="createtopic-content">
        <h2>Créer un nouveau Topic</h2>

        <div className="createtopic-input">
          <input id='topic-title' minLength="20" type="text" placeholder="Écrivez le titre de votre topic" required />
          <SetCategory />
        </div>

      </div>

      {!displayPreview? <TextEditor height={window.innerHeight - 240} text={text} setText={setText} />

      :<div className="createtopic-preview">
         <MessageCard message={new Message({ user_id: user.id, text_content: text})} />
      </div>}

      <div className="createtopic-footer">
        <div className="btn btn-grey" onClick={() => setDisplayPreview(!displayPreview)}>
          {displayPreview ? "Retour" : "Voir la preview du message"}
        </div>
        <button type="submit" className="btn">
          Poster le message
        </button>
      </div>
    </form>
  );
}

export default CreateTopic;
