import { useState, useEffect } from 'react';
import { AiOutlineCloudUpload, AiFillFile, AiFillFileImage, AiFillFileExcel, AiFillFilePdf, AiFillFileText, AiFillFilePpt, AiFillFileWord, AiFillFileMarkdown, AiFillFileZip, AiFillFileUnknown} from 'react-icons/ai';
import './DocumentSection.scss';
import { AiFillLike, AiFillDislike } from 'react-icons/ai';
import { Document } from 'models';
import DocumentPreview from './DocumentPreview/DocumentPreview';
import DocumentUpload from './DocumentUpload/DocumentUpload';

function DocumentSection({ category, minVote }) {
  const [currentDoc, setCurrentDoc] = useState(null);
  const [currentSection, setCurrentSection] = useState(1);
  const [displayUpload, setDisplayUpload] = useState(false);
  const [listFile, setListFile] = useState([]);
  const [reload, setReload] = useState(0);

  useEffect(() => {
    let tab = Document.data.filter(x => x.category_id === category.id);
    tab.sort((a, b) => {
      const x = a.mimetype;
      const y = b.mimetype;
      if (x>y) return 1;
      else if (x<y) return -1;
      return 0;
    })
    tab.sort((b, a) => a.mark - b.mark);

    setListFile(tab);
    if (currentDoc) setCurrentDoc(oldValue => tab.find(item => item.id === oldValue.id));
  }, [reload, category])

  const changeSection = (opt) => {
    setCurrentSection(opt);
    setCurrentDoc(null);
  }

  const DisplayFiles = () => {
    return listFile.map((item,index) => {
      let select = (currentDoc === item) ? "explorer-file--select" : "";
      if (item.section !== parseInt(currentSection)) return null;
      if (item.mark < parseInt(minVote)) return null;
      return <div className={`explorer-file ${select}`} key={index} onClick={() => {setCurrentDoc(item); setDisplayUpload(false); }}>
        <GetIcon mimetype={item.mimetype}/>
        <h3>{item.title}</h3>
        <div className="explorer-file-mark">
          {(item.mark >= 0) ? <AiFillLike style={{color: "#6ca55c"}} /> : <AiFillDislike style={{color: "red"}} />}
          <p>{item.mark}</p>
        </div>
      </div>
    })
  }

  return (
    <div className="document">
      <div className="document-explorer">
        <div className="document-explorer-header">
          <p onClick={()=>changeSection(1)} className={parseInt(currentSection)===1 ? "document-explorer-header--selected" : ""}>Cours</p> 
          <p onClick={()=>changeSection(2)} className={parseInt(currentSection)===2 ? "document-explorer-header--selected" : ""}>Exercices</p>
          <p onClick={()=>changeSection(3)} className={parseInt(currentSection)===3 ? "document-explorer-header--selected" : ""}>Fiches de révision</p>
        </div>

        <div className="document-explorer-all">
          <DisplayFiles />
        </div>
        <div className="btn" onClick={() => setDisplayUpload(true)}>
          <p>Partager un document</p> <AiOutlineCloudUpload />
        </div>
      </div>

      <div className="document-aside">
        {displayUpload ? <DocumentUpload 
        currentSection={currentSection} 
        setCurrentSection={setCurrentSection} 
        setReload={setReload} 
        category={category} 
        /> : 
        <DocumentPreview setReload={setReload} currentDoc={currentDoc} />}
      </div>
      
    </div>
  );
}

  // const api_key = "AIzaSyCt141dq3BdcfBX4u8B49mPR27eYpU9DGE";
  // fetch(`https://www.googleapis.com/drive/v3/files/${key}?key=${api_key}`)

  function GetIcon({ mimetype }) {
    mimetype = mimetype.toLowerCase();

    if (mimetype === "png" || mimetype === "jpeg" || mimetype === "jpg" || mimetype === "gif" || mimetype === "bmp") return <AiFillFileImage color='#28AEEA' />
    if (mimetype === "pdf") return <AiFillFilePdf color="#F76768" />
    if (mimetype === "txt") return <AiFillFileText />

    if (mimetype === "xls" || mimetype === "xlsx") return <AiFillFileExcel color="#3A9D3F" />
    if (mimetype === "ppt" || mimetype === "pptx" || mimetype === "ppsx") return <AiFillFilePpt color="#E64A19" />
    if (mimetype === "doc" || mimetype === "docx") return <AiFillFileWord color="#2B5795" />
    if (mimetype === "md") return <AiFillFileMarkdown />
    if (mimetype === "zip") return <AiFillFileZip />

    if (mimetype === "") return <AiFillFileUnknown />

    return <AiFillFile />
  }

export default DocumentSection;