import Prism from "prismjs";
import parse from 'html-react-parser';
import { useEffect, useContext } from 'react';
import './Message.scss';

import "prismjs/themes/prism-tomorrow.css";
import 'prismjs/components/prism-java';
import 'prismjs/components/prism-c';
import 'prismjs/components/prism-python';
import 'prismjs/components/prism-cpp';
import 'prismjs/components/prism-csharp';
import 'prismjs/components/prism-sql';
import 'prismjs/components/prism-markdown';
import 'prismjs/components/prism-php-extras';
import 'prismjs/components/prism-markup-templating';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-php';
import 'prismjs/components/prism-batch';
import 'prismjs/components/prism-asm6502';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js'
import "prismjs/plugins/line-numbers/prism-line-numbers.css"
//import 'prismjs/components/prism-plsql';

import { dateToString, dateToHour } from "utils/date";
import { User } from "models";
import UserIcon from "components/UserIcon/UserIcon";
import { FaCrown } from "react-icons/fa";
import { UserContext } from "Context";
import { BsFillShieldLockFill } from "react-icons/bs";

function Message({ message, author_id, children }) {

  const message_user = User.getUserById(message.user_id) || new User();
  const privilege = message_user.user_privilege;
  const [user] = useContext(UserContext);

  useEffect(() => {
    Prism.highlightAll();
  }, []);
  
  return (
    <div className={(user.id == message.user_id) ? "message box message--user" : "message box"}>
      <div className={"message-head"}>
        <div className="message-user">
          <UserIcon noname user_id={message.user_id} size={35} />
          
          <p>{message_user.first_name + " " + message_user.last_name} </p>

          {author_id == message.user_id && <FaCrown className="crown" />}
          {privilege == 2 && <BsFillShieldLockFill className="admin" />}
        </div>

        <div className="message-date">
          
          <p>{dateToString(message.creation_date) + " - " + dateToHour(message.creation_date)}</p>
          {message.modification > 0 && <p>/ (modifié {message.modification} fois)</p>}
          {children}
        </div>
      </div>
      <div className="message-body line-numbers">
        {parse(message.text_content)}
      </div>
    </div>
  );
}


export default Message;
