import './CategoryCard.scss';
import React from 'react';
import { Category } from 'models';
import { useHistory } from "react-router";

function CategoryCard({ category, mini }) {
  category = category || new Category();
  const background = category.background;
  const history = useHistory();

  const handleClick = e => {
    e.stopPropagation();
    history.push(`/categories/${category.path}`);
  }
  
  return (
    <div onClick={handleClick}
      style={{
        background: background, 
      }} 
      className={mini ? "card-category--mini card card--simple card-category" : "card card--simple card-category"}>

      <img src={category.icon} alt="category-icon"></img>
      <div className="card-category-text">
        <p>{category.title}</p>
      </div>
      
    </div>
  );
}

export default CategoryCard;
