import './UserIcon.scss';
import { Link, useHistory } from "react-router-dom";
import { User } from 'models';

function UserIcon({ user_id, size, full, noname }) {
  size = size || 50;
  const user = User.getUserById(user_id);
  const history = useHistory()

  const handleClick = e => {
    e.stopPropagation();
    e.preventDefault();

    history.push(`/user/${user_id}`)
  }
  return (
    <div className="usericon" onClick={handleClick}>
      <img src={user.icon} alt="user icon" width={size} height={size} />
      {!noname && <p>{user.first_name} {full ? user.last_name : ""}</p>}
    </div>
  );
}

export default UserIcon;
