import React from 'react'

const SubjectButton = ({subjectId, subject, catId, clickEvent, active}) => {

    const handleClick = (e) => {
       clickEvent(catId);
    }

    return (
        <div onClick={handleClick} className={active ? "subject-button subject-button-active" : "subject-button"}key={subjectId} id={`category${catId}`}>
            {subject}
        </div>
    )
}

export default SubjectButton
