import './RegisterInfo.scss';

function RegisterInfo({ setStep, step, setFirstName, setLastName, firstName, lastName, password, setPassword }) {
  
  const nextStep = (e) => {
    e.preventDefault();

    setStep(step + 1);
  }

  return <form onSubmit={nextStep}>
  <h2>Renseigner mes informations de connexion</h2>

  <div className="register-input">
    <label htmlFor="prenom">Prénom</label>
    <input 
      autoFocus 
      required 
      value={firstName} 
      onChange={(e) => setFirstName(e.target.value)} 
      type="text" 
      id="prenom" 
      placeholder="prénom"
      autoComplete="firstname"
    />

    <label htmlFor="nom">Nom de famille</label>
    <input 
      autoFocus 
      required 
      value={lastName} 
      onChange={(e) => setLastName(e.target.value)} 
      type="text" 
      id="nom" 
      placeholder="nom"
      autoComplete="lastname"
    />

    <label htmlFor="password">Mot de passe</label>
    <input 
      minLength="8" 
      required 
      autoFocus
      value={password} 
      onChange={(e) => setPassword(e.target.value)} 
      autoComplete="new-password"
      type="password" 
      id="password" 
      placeholder="mot de passe"
    />

    <label htmlFor="confirm-password">Confirmer votre mot de passe</label>
    <input 
      required
      type="password" 
      id="confirm-password" 
      placeholder="confirmer le mot de passe"
      autoComplete="new-password"
      onChange={(e) => {
        if (e.target.value !== password) e.target.setCustomValidity('Les mots de passes ne correspondent pas.');
        else e.target.setCustomValidity('');
      }}
    />
  </div>

  <div className="register-button">
    <div className="btn btn-grey" onClick={() => setStep(step - 1)}>Précédent</div>
    <button className="btn" type="submit">Suivant</button>
  </div>
</form >

}

export default RegisterInfo;
