import './FavoriteCategories.scss';
import { useContext } from 'react';
import { Category, Favorite } from 'models';
import { UserContext } from "Context";
import CategoryCard from 'components/CategoryCard/CategoryCard';

function FavoriteCategories() {
  const [user] = useContext(UserContext);
  let categories = Favorite.data.filter(item => item.user_id == user.id);
  categories = categories.map(item => Category.getCategoryById(item.category_id));

  const Display = () => {
    return categories.map((item,index) => {
      return <CategoryCard category={item} key={index} />
    })
  }

  return <div className="page favorite">
    <h1>Mes catégories préférées</h1>
    <div className="wrapper">
      <Display />
    </div>
  </div>
}

export default FavoriteCategories;
