import { Category, Class } from 'models';
import './RegisterClass.scss';
import {useState } from "react";

function RegisterClass({ setStep, step, setClasse, classe, setFavorite }) {
  
  const [department, setDepartment] = useState("");
  const [semester, setSemester] = useState("");

  const nextStep = (e) => {
    e.preventDefault();

    if (classe !== "") {
      setFavorite([]);
      setStep(step + 1);
    }
  }

  const OptionDepartment = () => {
    const departments = Category.data.filter(item => !item.category_id);

    return departments.map((item, index) => <option key={index} value={item.id}>{item.title}</option>);
  }

  const OptionSemester = () => {
    const semesters = Category.data.filter(item => item.category_id === parseInt(department));

    return semesters.map((item, index) => <option key={index} value={item.id}>{item.title}</option>);
  }

  const OptionGroup = () => {
    const groups = Class.data.filter(item => item.department_id === parseInt(department) && item.semester_id === parseInt(semester));

    return groups.map((item, index) => <option key={index} value={item.id}>{item.title}</option>);
  }

  return <form onSubmit={nextStep}>
  <h2>Choisissez votre classe</h2>

  <div className="register-input">
    
    <select value={department} onChange={(e) => { setDepartment(e.target.value); setSemester(""); setClasse(""); }} required>
      <option value="">Selectionnez un département</option>
      <OptionDepartment />
    </select>

    <select value={semester} disabled={department === ""} onChange={(e) => { setSemester(e.target.value); setClasse(""); }} required>
      <option value="">Selectionnez un semestre</option>
      <OptionSemester />
    </select>


    <select value={classe} disabled={semester === ""} onChange={(e) => setClasse(e.target.value)} required>
      <option value="">Selectionnez un groupe</option>
      <OptionGroup />
    </select>
 

  </div>

  <div className="register-button">
    <div className="btn btn-grey" onClick={() => setStep(step - 1)}>Précédent</div>
    <button className="btn" type="submit">Suivant</button>
  </div>
</form>

}

export default RegisterClass;
