import React from 'react';
import { MdKeyboardArrowRight } from 'react-icons/md';
import './Home.scss';

import CategoryCard from 'components/CategoryCard/CategoryCard';
import AddTopic from './AddTopic/AddTopic';
import TopicCard from './TopicCard/TopicCard';
import { Topic, Category, Favorite } from "models";
import { UserContext } from 'Context';
import { useContext } from "react";
import { useHistory } from 'react-router';

function Home() {
  const [user] = useContext(UserContext);
  const history = useHistory();

  const DisplayFavoriteCategories = () => {
    const favoriteCatgories = Favorite.getFavoritebyUser(user?.id);
    return favoriteCatgories.map((item,index) => {
      if (index > 2) return null;
      return <CategoryCard category={Category.getCategoryById(item.category_id)} key={index} />
    });
  }

  const DisplayRecentTopics = () => {
    const topics = Topic.data;

    topics.sort((a, b) => b.creation_date - a.creation_date);

    return topics.map((item,index) => {
      if (index > 2) return null;
      return <TopicCard topic={item} key={index} />;
    })
  }

  return (
    <div className="page home">

      <div style={{ height: "35%", maxHeight: "35%"}} className="home-container">
        <AddTopic />
      </div>

      <div className="home-container">
        <div className="home-container-title">
          <h2>Mes catégories préférées</h2>
          <MdKeyboardArrowRight onClick={() => history.push("/favorite-categories")} className="btn btn-icon btn-lightgrey" />
        </div>
        
        <div className="favorite">
          <DisplayFavoriteCategories />
        </div>
      </div>

      <div style={{height: "40%"}} className="home-container">
        
        <div className="home-container-title">
          <h2>Derniers topics postés</h2>
          <MdKeyboardArrowRight onClick={() => history.push('/forum')} className="btn btn-icon btn-lightgrey" />
        </div>
        <div className="recenttopic">
          <DisplayRecentTopics />
        </div>
        
      </div>
      
    </div>
  );
}

export default Home;
