import './Topic.scss';
import { Category } from 'models';
import UserIcon from 'components/UserIcon/UserIcon';
import CategoryCard from 'components/CategoryCard/CategoryCard';
import { dateToString } from "utils/date";
import { AiFillQuestionCircle, AiFillMessage } from "react-icons/ai";
import { useHistory } from "react-router-dom";
import { AiFillLike, AiFillDislike } from 'react-icons/ai';

function Topic({ topic }) {
  const category = Category.getCategoryById(topic.category_id);
  const title = topic.title.replace(" ?","").replace(/\s/g, '-')
  const url = `/forum/${topic.id}/${title}`;
  const history = useHistory();

  const DisplayStatus = () => {
    return <span>
      <AiFillQuestionCircle style={{color: "#e14e4e"}}  /> 
      N
    </span>
  }

  return <tr onClick={() => history.push(url)} className="topic">

    <td className="topic-icon"><UserIcon user_id={topic.user_id} noname size={35} /></td>
    <td className="topic-title"><p>{topic.title}</p></td>
    <td className="topic-date">{dateToString(topic.creation_date)}</td>
    <td className="topic-status">
      <span><DisplayStatus /></span>
      <span><AiFillMessage style={{color: "grey"}} />{topic.nb_msg}</span>
      <span>
        {(topic.mark >= 0) ? <AiFillLike style={{color: "#6ca55c"}} /> : <AiFillDislike style={{color: "red"}} />}
        {topic.mark}
      </span>
    </td>
    <td className="topic-category"><CategoryCard category={category} mini /></td>
  </tr>
}

export default Topic;
