import './EditMark.scss';
import { Mark, Class, Category } from "models/index";
import { useState, useContext, useEffect } from "react";
import SelectCategory from 'components/SelectCategory';
import { UserContext } from "Context";
import CardMark from "components/CardMark/CardMark";
import { useHistory, useParams } from 'react-router';

function EditMark() {
    const history = useHistory();
    const [grade, setGrade] = useState(10);
    const [coef, setCoef] = useState(1);
    const [semester, setSemester] = useState(null);
    const [category, setCategory] = useState(null);
    const [title, setTitle] = useState("DS");
    const [max, setMax] = useState(20);
    const [user] = useContext(UserContext);
    const dept = Class.getClassById(user.class_id).department_id;
    const [old, setOld] = useState(false);
    const { id } = useParams();


    const addMark = async (e) => {
        e.preventDefault();

        let new_mark = new Mark({
            id: old.id,
            title: title,
            grade: parseFloat(grade),
            category_id: parseInt(category),
            creation_date: new Date(),
            coef: parseInt(coef),
            max: max,
            user_id: user.id,
            description: Category.getCategoryById(parseInt(category)).module
        });

        if (old) await Mark.update(new_mark);
        else await Mark.post(new_mark);

        history.push("/mark");
    }

    const deleteMark = async () => {
        await Mark.delete(old.id);
        history.push("/mark");
    }

    useEffect(() => {
        const c_mark = Mark.getMarkById(parseInt(id));
        if (c_mark) {
            setOld(c_mark);
            setGrade(c_mark.grade);
            setCoef(c_mark.coef);
            setSemester(Category.getCategoryById(parseInt(c_mark.category_id))?.category_id);
            setCategory(c_mark.category_id);
            setTitle(c_mark.title);
            setMax(c_mark.max);
        };
    }, [id])

    return <div className="page editmark">

        <main className="box">
            <form onSubmit={addMark}>
                <label>Semestre</label>
                <SelectCategory setCategory={setSemester} category={semester} parent_id={dept}  />
                

                <label>Category</label>
                <SelectCategory category={category} setCategory={setCategory} parent_id={semester} />

                <label>Note</label>
                <input autoFocus id="grade" type="number" placeholder="Note" value={grade} onChange={e => {
                    const n = parseInt(e.target.value)
                    setGrade(n)
                    if (n > max) setMax(n);
                }} />

                <label>Maximum</label>
                <input id="max" type="number" placeholder="Note maximum" value={max} onChange={e => setMax(e.target.value)} />

                <label>Titre de la note</label>
                <input id="title" type="text" placeholder="Titre du contrôle" value={title} onChange={e => setTitle(e.target.value)} />

                <label>Coefficient</label>
                <input autoFocus id="coef" type="number" placeholder="Coef" value={coef} onChange={e => setCoef(e.target.value)} />

                <span>
                    <div onClick={() => history.goBack()}className="btn btn-grey">Retour</div>
                    <button disabled={!category} className="btn" type="submit">{old ? "Modifier la note" : "Ajouter la note"}</button>
                    {old && <div onClick={deleteMark} className="btn btn-red">Supprimer</div>}
                </span>
            </form>

            <aside>
                <CardMark size={200} mark={{ title: title, category_id: category, grade: grade, max: max }} />
            </aside>
        </main>

    </div>
}

export default EditMark;
