import { Category, Class } from 'models';
import './RegisterFavorite.scss';

import SubjectButton from './SubjectButton';

function RegisterFavorite({ setStep, step, classe, setFavorite, favorite }) {
  
  const userClass = Class.data.find(item => item.id === parseInt(classe));
  const maxFavorite = 3;

  const nextStep = (e) => {
    e.preventDefault();

    setStep(step + 1);
  }

  const CheckCategory = () => {
    const categories = Category.data.filter(item => item.category_id === userClass.semester_id)

    return categories.map((item, index) => {

      const handleChange = (catId) => {
          let newFav = [...favorite];
            if(!(favorite.find(fav => fav === catId))){
              if(favorite.length < maxFavorite) {
                newFav.push(catId)
              };
            }else{
              if (index > -1)  {
                newFav.splice(newFav.indexOf(catId), 1);
              }
            }
            setFavorite(newFav);
      }

        return (
          <SubjectButton subject={item.title} subjectId={index} catId={item.id} activeColor={item.color} clickEvent={handleChange} active={favorite.find(fav => fav === item.id)}/>
        )
    })
  }

  return <form onSubmit={nextStep}>
    <h2>Choisissez 3 matières qui vous posent des difficultés</h2>

    <div className="register-input">

      <div className="register-wrapper">
          <CheckCategory />
      </div>

      <span className="register-count">{favorite.length} / {maxFavorite}</span>

    </div>

    <div className="register-button">
      <div className="btn btn-grey" onClick={() => setStep(step - 1)}>Précédent</div>
      <button className="btn" disabled={favorite.length < maxFavorite} type="submit">Suivant</button>
    </div>
  </form>

}

export default RegisterFavorite;
