import './Options.scss';
import { AiOutlineLogout } from "react-icons/ai";
import { UserContext } from 'Context';
import { useContext } from "react";
import { themes, setThemes } from "utils/themes";
import { CalendarEvent } from 'models';
import { Link } from "react-router-dom";

function Options() {
  const [,setUser] = useContext(UserContext);

  const disconnect = () => {
    setUser(null);
    CalendarEvent.data = [];
    window.localStorage.clear();
  }

  const DisplayTheme = () => {
    return themes.map((item,index)=> {

      const onClick = (item,index) => {
        setThemes(item)
        window.localStorage.setItem("theme",index);
      }

      return <div className="theme card card--simple" onClick={() => onClick(item,index)} key={index}>
        <div className="theme-circle" style={{background: item.back}}></div>
        <p>{item.name}</p>
      </div>
    })
  }
  
  return (
    <div className="page options">

      <h2>Liste des thèmes</h2>
      <div className="gridtheme">
        <DisplayTheme />
      </div>

      <div>
        <h2>Mentions légales</h2>
        <Link to="/mention" className='btn'>Voir les mentions légales</Link>
      </div>
      <div className="gestion">
        <h2>Gestion de compte</h2>
        <Link to="/me/edit" className="btn">
          <p>Modifier mon compte</p>
        </Link> 
        <div onClick={disconnect} className="gestion-disconnect btn btn-red">
          <p>Se deconnecter</p>
          <AiOutlineLogout />
        </div> 
      </div>

    </div>
  );
}

export default Options;
