import { useState, useContext, useEffect } from 'react';
import './Marks.scss';
import { Mark, Class, Category, User } from "models";
import CardMark from 'components/CardMark/CardMark';
import { UserContext } from "Context";
import { AiOutlinePlus } from "react-icons/ai";
import { BiArrowBack } from "react-icons/bi";

import SelectCategory from 'components/SelectCategory';
import StatsMark from './StatsMark/StatsMark';
import { Link } from 'react-router-dom';

function Marks() {
  const [marks, setMarks] = useState([]);

  const [user, setUser] = useContext(UserContext);
  const user_class = Class.getClassById(user.class_id);
  const [semester, setSemester] = useState(user_class.semester_id);
  const [category, setCategory] = useState("");
  const [displayEmpty, setDisplayEmpty] = useState(false);

  useEffect(() => {
    const fetchMark = async () => {
      if (user && !marks.length) {
        await Mark.getAll(user.id);
        setMarks(Mark.data);
      }
    }

    fetchMark();
  }, [user])

  let array = Mark.averageModule(semester, displayEmpty)
  let average = Mark.averageMark(array);

  const DisplaySemester = () => {
    const dept = Class.getClassById(user.class_id).department_id;
    const tab = Category.data.filter(item => item.category_id == dept);
    return tab.map((item, index) => {
      return <div 
      key={index} 
      className={item.id == semester ? "btn" : "btn btn-grey"} 
      onClick={() => {
        setCategory("");
        setSemester(item.id)}
      }>{item.title}</div>
    })
  }
  
  const DisplayTomuss = () => {
    const tab = marks.filter(item => Category.getCategoryById(item.category_id)?.category_id == semester);
    return tab.map((item, index) => {
      return <CardMark mark={item} show={Category.getCategoryById(item.category_id)} key={index} />
    })
  }

    const DisplayGrade = () => {
      let tab;

      if (category == "") {
        const modules = Category.data.filter(item => item.category_id == semester).map(item => item.id);
        tab = marks.filter(mark => modules.includes(mark.category_id));
      } else {
        tab = marks.filter(mark => mark.category_id == category);
      }
      

      return tab.map((mark, i) => {
        return <CardMark mark={mark} key={i} />
      })
    }

    const sendTomuss = async e => {
      e.preventDefault();
      const input_url = document.getElementById("tomuss_url").value;
      const tab = input_url.split("/");
      const token = tab[tab.length-1];
      
      if (!token || token == "") return;
      
      await User.update_tomuss({
        user_id: user.id,
        tomuss: token
      });

      const newUser = {...user};
      newUser.tomuss = token;
      setUser({...newUser});
    }

  if (user.tomuss == "" || !user.tomuss) {
    return <div className="page nomarks">
      <h2>Vos notes</h2>
      <p>
        Pour acceder à vos notes, vous devez insérer votre flux RSS Tomuss<br></br>
        Les notes récoltées via ce lien ne seront consultable uniquement par vous et resteront confidentielles<br></br>
        Un tutoriel vous explique ci-dessous comment obtenir ce lien
      </p>

      <form onSubmit={sendTomuss}>
        <input id="tomuss_url" placeholder='lien du flux rss' type="text" />
        <button type='submit' className='btn'>Envoyer</button>
      </form>

      <iframe width="560" height="315" src="https://www.youtube.com/embed/EqgUfMvY6Do" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>

    </div>
  }

  return <div className="page marks">

    <div className="head">
      <div>
        <DisplaySemester />
      </div>
    </div>

    <div className="wait box">
      <h2>Dernières notes</h2>
      <div className="wrapper">
        <DisplayTomuss />
      </div>
    </div>

    <div className="final box">
      <span>
        <div>
          <div className='average'>Moyenne générale : <b>{average}</b></div>
          
          <p>Afficher tous les modules : <input 
          value={displayEmpty} 
          onChange={() => setDisplayEmpty(!displayEmpty)} 
          type="checkbox" /></p>
        </div>
        <div>
          {category && <div onClick={() => setCategory(false)} className="btn btn-grey btn-circle"><BiArrowBack /></div>}
          <SelectCategory category={category} setCategory={(c) => setCategory(c) } parent_id={semester} />
          <Link to="/mark/-1" className="btn btn-circle"><AiOutlinePlus /></Link>
        </div>
      </span>

      <div className="final-wrapper">
        {category == "" ? <StatsMark semester={semester} displayEmpty={displayEmpty} setCategory={setCategory} /> : <DisplayGrade />}
      </div>
    </div>

  </div>
}

export default Marks;
