import fetchData from "utils/fetchData";

class Message {

    static data = [];

    // constructor
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id;

        this.topic_id = opt.topic_id || null;
        this.user_id = opt.user_id;
        this.conv_id = opt.conv_id || null;

        this.text_content = opt.text_content || "";
        this.creation_date = new Date(opt.creation_date) || new Date();
        this.modification = opt.modification || 0;
    }

    // statics methods
    static post = async (object) => {
        const data = await fetchData({
            method: 'post',
            url: 'api/message',
            data: object
        })

        return data;
    }

    // getters
    static getMessageById = (id) => {
        return Message.data.find(x => x.id === id);
    }

    static getMessageByPath = (path) => {
        return false;
    }

    static getMessagesByTopicId = async (id) => {
        const data = await fetchData({ url: "api/message/topic/" + id });
        return data.map(element => new Message(element));
    }

    static getMessagesByConvId = async (id) => {
        const data = await fetchData({ url: "api/message/conversation/" + id });
        return data.map(element => new Message(element));
    }

    static update = async (props) => {
        const data = await fetchData({
            method: 'put',
            url: 'api/message',
            data: props
        })

        return data;
    }

    static delete = async (props) => {
        const { id } = props;
        await fetchData({
            method: 'delete',
            url: 'api/message/' + id
        })
    }

}

export default Message;