import { Category } from 'models';

function SelectCategory({ parent_id, setCategory, category }) {

    const DisplayOption = () => {
        const tab = Category.data.filter(item => item.category_id == parent_id);
        return tab.map((item, index) => <option value={item.id} key={index}>{item.title}</option>)
    }

    return <select onChange={e => setCategory(e.target.value)} value={category}>
        <option value={""}>- modules -</option>
        <DisplayOption />
    </select>
}

export default SelectCategory;
