

import { Link } from 'react-router-dom';
import { AiFillStar } from 'react-icons/ai';
import { FaUserCog } from 'react-icons/fa';
import { ImNewspaper, ImClubs } from 'react-icons/im';

import MiniCalendar from 'components/MiniCalendar/MiniCalendar';
import { Conversation, Category, Class } from 'models';
import { useEffect, useState, useContext } from 'react';

import './Aside.scss';
import { UserContext, TreeContext, NotifContext } from "Context";
import UserIcon from 'components/UserIcon/UserIcon';
import JSConfetti from 'js-confetti'

const jsConfetti = new JSConfetti()

function Aside({ children }) {
  const [user] = useContext(UserContext);
  const [tree] = useContext(TreeContext);
  const userClass = Class.getClassById(user?.class_id);
  const userSemester = Category.getCategoryById(userClass?.semester_id)?.title;
  const userGroup = userClass?.title;
  const [notif, setNotif] = useContext(NotifContext);

  // fetch conversation
  const getConversations = async () => {
    let result = await Conversation.getAllByUserID(user.id);
    setNotif(prev => ({ ...prev, conv: result}));
  }

  if (!notif.conv && user) getConversations();
  
  useEffect(() => {
    if (user.socket) {
      user.socket.on('get-conv', () => {
        getConversations();
    });
    }
  }, [user])

  const DisplayLink = () => {
    return tree?.map((item,index)=>{
      if (item.display !== 3) return null;

      let class_select = item.selected ? "link-container--selected" : "";

      const DisplayNotif = () => {
        let count = 0;
        notif.conv?.forEach((element) => {
          if (!(notif.dateConv.find(item => item.id == element.id && new Date(item.date) >= new Date(element.last_message)))) {
            count++;
          }
        })
        if (count == 0) return null;
        return <div className="notif">{count}</div>
      }
      
      return <Link to={item.path} className={"link-container " + class_select} key={index}>
        <item.icon />
        {(item.path == "/private-messages") && <DisplayNotif />}
      </Link>
    })
  }


  const DisplayNormal = () => {
    return <>
        <MiniCalendar />
        <div className="activity">
          <h3>Accès rapide</h3>
          <div className="activity-content">
            <Link to="/favorite-categories" className="activity-content-item">
              <AiFillStar style={{color: "#ffca28"}} />
              <p>Mes catégories préférées</p>
            </Link>
            <Link to="/options" className="activity-content-item">
              <FaUserCog style={{color: "grey"}} />
              <p>Mon profil</p>
            </Link>
            <a href="https://www.univ-lyon1.fr/actualites" target="_blank" rel="noopener noreferrer" className="activity-content-item">
              <ImNewspaper style={{color: "red"}} />
              <p>Les news de l'IUT</p>
            </a>
            <div onClick={() => {
              jsConfetti.addConfetti({}) // { emojis: ['❤️']  } // 
            }} className="activity-content-item">
              <ImClubs style={{color: "green"}} />
              <p>Ne pas cliquer</p>
            </div>
          </div>
        </div>
    </>
  }



  return (
    <div className="aside">

        <div className="identite">
            <div className="identite-picture">
              <UserIcon size={130} user_id={user.id} full />
            </div>
            <b>{userSemester} - {userGroup}</b>
        </div>

        <div className="link">
          <DisplayLink />
        </div>

        {!children && <DisplayNormal />}

        {children}

    </div>
  );
}

export default Aside;
