import './MiniCalendar.scss';
import { Category, Class } from 'models';
import { Link } from 'react-router-dom';
import { CalendarEvent } from 'models';
import { useEffect, useState, useContext } from 'react';
import { UserContext } from "Context";
import { dateToHour, dateToDay } from 'utils/date';
const { moduleToCategory } = require("utils/module");

const MiniCalendar = ({ category_id }) => {
  const [listEvent, setListEvent] = useState(CalendarEvent.data);
  const [user] = useContext(UserContext);

  useEffect(() => {
    const getCalendarData = async () => {
      const group = user ? Class.getClassById(user?.class_id) : null;
      const link = group ? group.ics_link : undefined;
      await CalendarEvent.getAll(link);

      setListEvent(CalendarEvent.data)
    }

    if (!CalendarEvent.data.length) getCalendarData();
  }, [user]);

  const CalendarCard = ({ calendar }) => {
    const category = Category.getCategoryById(calendar.category_id);
    if (!category) return null;
    const hour = dateToHour(calendar.start);

    return (
      <Link to={`/categories/${category.path}`} className="calendarcard">
        <p className="calendarcard-hour">{hour}</p>
        
        <div className="calendarcard-text" style={{borderLeft: `solid 3px ${category.color}`}}>
          <p className="calendarcard-text-description">{calendar.location}</p>
          <p className="calendarcard-text-category">{category.title}</p>
        </div>
      </Link>
    );
  }

  const DisplayCalendarCard = () => {
    let tab = listEvent.filter(item => {
      return item.start.getTime() >= new Date().getTime();
    });
  
    tab.sort((a, b) => a.start - b.start);
    tab = tab.map(item => {
      item.category_id = moduleToCategory(item.summary);
      return item;
    })
    tab = tab.filter(item => item.category_id !== -1);

    if (category_id) {
      const oldtab = [...tab];
      tab = tab.filter(item => item.category_id === category_id);
      if (!tab.length) tab = oldtab;
    }

    return tab.map((item,index)=> {
      if (index > 2) return null;
      return <CalendarCard calendar={item} key={index}/>
    })
  }
  
  return <div className="calendar">
    <div className="calendar-title">
      <h3>Cours à venir</h3>
      <p>| {dateToDay(new Date())}</p>
    </div>

    <div className="calendar-all">
      <DisplayCalendarCard />
    </div>
  </div>
}


export default MiniCalendar;
