import './DocumentUpload.scss';
import {useState, useContext } from "react";
import DragAndDrop from "./DragAndDrop";
import { Document } from 'models';
import { UserContext } from 'Context';

const config = {
  allowedFileFormats: ["image/jpeg", "image/jpg", "image/png"],
  fileSizeMBLimit: 10, // mb
  filesLimit: 1
};

const axios = require("models/axios");

function DocumentUpload({ category, setReload, setCurrentSection, currentSection }) {
  const [currentFile, setCurrentFile] = useState(null);
  const [title, setTitle] = useState("");
  const [loading, setLoading] = useState(false);
  const [user] = useContext(UserContext);


  const postFile = (e) => {
    e.preventDefault();

    setLoading(true);

    const formData = new FormData();
    formData.append('file', currentFile);
    formData.append('parent', "1IFLwhJtaJB435kvoiHqkPMhXJs38wy8A");
    formData.append("file_name", title);

    axios.post('/api/drive', formData, { 
      headers: { 'Content-Type': 'multipart/form-data' },
    })
    .then((response) => {
      if(response.data.err){
        alert("Erreur lors de l'envoi du fichier sur la base de données.");
        setLoading(false);
        resetInput();
      } else {
        postDocument(response.data);
      }
    })
  }

  const resetInput = () => {
    setTitle("");
    setCurrentFile(null);
  }

  const postDocument = async (docId) => {
    let re = /(?:\.([^.]+))?$/;
    const mimetype = re.exec(currentFile?.name)[1];

    await Document.post({
      user_id: user.id,
      category_id: category.id,
      section: parseInt(currentSection),
      title: title,
      mimetype: mimetype,
      drive_key: docId
    })

    await Document.getAll();
    setLoading(false);
    resetInput();
    setReload(oldVal => oldVal + 1);
  }

  return <form onSubmit={postFile} className="document-upload">
    
    <div className="document-upload-container">
      <label htmlFor="title">Titre du document</label>
      <input value={title} onChange={e => setTitle(e.target.value)} required type="text" id="title" placeholder="exemple: les matrices - chapitre 1" />
    </div>

    <div className="document-upload-container">
      <label htmlFor="typedoc">Type du document</label>
      <select id="typedoc" value={currentSection} onChange={(e) => setCurrentSection(e.target.value)} required>
        <option value={1}>Cours</option>
        <option value={2}>Exercices</option>
        <option value={3}>Fiche de révision</option>
      </select>
    </div>

    
    <div className="document-upload-container">
      <label>Fichier</label>
      <input type="file" onChange={e => setCurrentFile(e.target.files[0])} />
      <p>ou</p>
      <DragAndDrop setCurrentFile={setCurrentFile} config={config}>
          {currentFile ? <p className="active">{currentFile.name}</p> : <p>Glisser déposer votre fichier ici</p>}
      </DragAndDrop>

      
    </div>

    {loading ? <button disabled className="btn btn-grey">Chargement..</button> : 
    <button type="submit" disabled={!(currentFile)} className="btn">Envoyer mon document</button>}

  </form>
}

export default DocumentUpload;
