import fetchData from "utils/fetchData";

class Topic {

    static data = [];

    // constructor
    constructor(opt) {
        if (!opt) opt = {};

        this.id = opt.id;

        this.user_id = opt.user_id;
        this.category_id = opt.category_id;
        
        this.title = opt.title || "Unknown";
        this.status = opt.status || 1;
        this.creation_date = new Date(opt.last_message) || new Date();
        this.nb_msg = opt.nb_msg || 0;
        this.mark = opt.mark || 0;
    }

    get styleStatus() {
        let text, color;

        switch (this.status) {
            case 2:
                text = "Résolu"; 
                color = "34A76F";
            break;
            case 3:
                text = "Fermé"; 
                color = "F85640";
            break;
            default:
                text = "Non-Resolu";
                color = "FBA63B";
            break;
        }

        return {text: text, color: color}
    }

    static post = async(topic) => {
        const response = await fetchData({
            method: 'post',
            url: 'api/topic',
            data: topic
        })

        if (Array.isArray(response)) return false;

        return response;
    }


    static update = (object) => {
        return false;
    }

    static delete = async (props) => {
        const { id } = props;
        await fetchData({
            method: 'delete',
            url: 'api/topic/' + id
        })
        await Topic.getAll();
    }


    // getters
    static getTopicById = (id) => {
        return Topic.data.find(x => x.id == id);
    }

    static getMarkById = async(props) => {
        const { user_id, topic_id  } = props;
        const data = await fetchData({
            method: 'get',
            url: `api/rating/topic?user_id=${user_id}&document_id=${topic_id}`
        })

        let mark = 0;
        if (data[0]) {
            mark = data[0].mark;
        }
        return mark;
    }

    static getAll = async () => {
        const data = await fetchData({ url: "api/topic" });
        Topic.data = data.map(element => new Topic(element));
    }

    static postMark = async(obj) => {
        await fetchData({
            method: 'post',
            url: 'api/rating',
            data: obj
        })

        await Topic.getAll();
    }
}

export default Topic;