import React, { useEffect, useState, useContext } from 'react'
import './Chat.scss';
import { Message } from 'models';
import MessageCard from 'components/Message/Message';
import { UserContext, NotifContext } from "Context";
import { useParams } from 'react-router';

import { MdSend } from "react-icons/md";


const Chat = ({ setReload }) => {
    const [user] = useContext(UserContext);
    const [notif, setNotif] = useContext(NotifContext);
    const [messages, setMessages] = useState([]);
    const { id } = useParams();
    
    const scrollToBottom = () => {
        document.getElementById("container").scrollIntoView({ behavior: 'smooth' })
    }

    const sendMessage = async (e) => {
        e.preventDefault();

        const textarea = document.getElementById("textarea");
        
        const msg = new Message({ conv_id: parseInt(id), user_id: user.id, text_content: textarea.value });

        await Message.post(msg);

        const conversation = notif.conv.find(item => item.id == id);

        user.socket.emit('post-message', conversation?.user_id);

        textarea.value = "";

        setDateConv();
        setMessages(old => [...old, msg]);
        setReload(old => old + 1);
        scrollToBottom();
    }

    const getMessages = async () => {
        const res = await Message.getMessagesByConvId(parseInt(id));
        setMessages(res)
        scrollToBottom();
    }

    const setDateConv = () => {
        let tab = [...notif.dateConv];
        tab = tab.filter(item => item.id != id);
        tab.push({id: id, date: new Date().toUTCString()})

        if (JSON.stringify(tab) !== JSON.stringify(notif.dateConv)) {
            setNotif(prev => ({...prev, dateConv:[...tab] }));
        }
    }
    
    useEffect(() => {
        if (id) {
            getMessages();
            setDateConv();
        }
     }, [id])
     

     useEffect(() => {
        if (!id) return;
        
        getMessages();
 
        user.socket.emit("join-conv", id);

        user.socket.on('get-message', () => {
            getMessages();
            setDateConv();
            

            console.log("new message");
        });
 
         return () => {
             user.socket.emit("leave-room")
         }
     }, [id])


    const DisplayMessage = () => {
        return messages.map((item,index) => {
            return <MessageCard message={item} key={index}>

            </MessageCard>
          })
    }



    if (!id) return <div className="chat">
        <p>Aucune conversation selectionnée</p>
    </div>

    return (
        <div className="chat">
            
            <div className="chat-messages">
                <DisplayMessage />
                <div id="container"></div>
            </div>

            <form onSubmit={sendMessage}>
                <textarea id="textarea" type="text" placeholder="Votre message"></textarea>
                <div className="chat-submit">
                    <button className="btn btn-circle" type="submit"><MdSend /></button>
                </div>
            </form>
        </div>
    )
}

export default Chat