import React from 'react'
import './Verification.scss'

import { useParams,Link } from 'react-router-dom';
import { useEffect, useState } from 'react';

const axios = require("models/axios");

const Verification = () => {

  const [state, setState] = useState(0);

  let { id } = useParams();

  useEffect(() => {
    verif(id);

    document.title = "Vérification";
  }, [id]);

  const verif = (verifId) => {
    axios({
      method: "post",
      url: "api/user/verification",
      data: { id: verifId}
    }).then((response) => {
      if(response.data.err){
        console.log(response.data.err);
        setState(2);
      } else {
        setState(1);
      }
    })
  }

  return (
    <div className="centerpage">
     
     <div className="centerpage-main card">
      
        {state === 0 && <h2 style={{color: "grey"}}>Chargement en cours..</h2>}
        {state === 1 && <h2 style={{color: "#81f75d"}}>Verification Réussi</h2>}
        {state === 2 && <h2 style={{color: "red"}}>La vérification n'a pas fonctionné.</h2>}

        <Link className="btn" to="/login">Retour à la page de connexion</Link>
     </div>


    </div>
  )
}

export default Verification
