import './Login.scss';
import { useState, useEffect, useContext } from 'react';

import laptop from "assets/laptop.png";
import { Link } from 'react-router-dom';
import { UserContext } from 'Context';
import auth from 'utils/auth';
import JSConfetti from 'js-confetti'
const jsConfetti = new JSConfetti()

function Login() {

  const [keep, setKeep] = useState(true);
  const [, setUser] = useContext(UserContext);

  useEffect(() => {
    document.title = "Se connecter";
  }, []);

  const authUser = async (mail, password) => {

    const result = await auth(mail, password);
    if (result) {
      jsConfetti.addConfetti()
      setUser(result);
    } else {
      alert("Identifiant ou mot de passe inccorect");
    }
  }

  const verifyInput = (event) => {
    event.preventDefault();
  
    let mail = document.getElementById("mail").value;
    let password = document.getElementById("password").value;

    if (!mail.includes('@etu.univ-lyon1.fr')) mail = mail + "@etu.univ-lyon1.fr";

    authUser(mail,password);
  }

  return (
    <div className="page login">
      <form onSubmit={verifyInput}>
          <img className="login-laptop" src={laptop} alt="description" />
          <div className="login-deux">
              <div className="login-mail">
                <h2>Veuillez entrer vos identifiants</h2>
                <div className="login-arobase">
                  <input 
                    type="text" 
                    id="mail" 
                    required 
                    placeholder="prenom.nom"
                    autoComplete="username"
                  />
                  <p>@etu.univ-lyon1.fr</p>
                </div>
              </div>

              <div className="login-password">
                <input 
                  minLength="8" 
                  required 
                  type="password" 
                  id="password" 
                  placeholder="Mot de passe" 
                  autoComplete="current-password"
                />
                
                <button className='btn' type="submit">Se connecter !</button>
              </div>

              <div className="login-link">
                <div className="login-keep">
                  <input checked={keep} onChange={() => setKeep(!keep)} type="checkbox"  id='keep' />
                  <label htmlFor='keep'>Se souvenir de moi</label>
                </div>
                <Link to="/register">Pas de compte ? Inscrivez-vous !</Link>
                <Link to="/resetpassword">Mot de passe oublié ?</Link>
              </div>
          </div>
      </form>


    </div>
  );
}

export default Login;
