// contexte de l'utilisateur
const UserContext = createContext();
const UserProvider = (props) => {
    const [user, setUser] = useState(null);  
    return <UserContext.Provider value={[user, setUser]}>{props.children}</UserContext.Provider>
};
export { UserContext, UserProvider };

const NotifContext = createContext();
const NotifProvider = (props) => {
    const defaultValue = (window.localStorage.getItem("date-conv")) ? JSON.parse(window.localStorage.getItem("date-conv")) : [];
    const [notif, setNotif] = useState({
      dateConv: defaultValue,
      conv: null
    });

    useEffect(() => {
      window.localStorage.setItem("date-conv", JSON.stringify(notif.dateConv));
    }, [notif.dateConv]);

    return <NotifContext.Provider value={[notif, setNotif]}>{props.children}</NotifContext.Provider>
};
export { NotifContext, NotifProvider };

