import React from 'react'
import { useState, useEffect } from 'react';
import {useParams} from 'react-router-dom';
import { Link } from "react-router-dom";
import "./ResetPassword.scss";

const axios = require("models/axios");

const ResetPasswordOn = () => {

    let { id } = useParams();

    useEffect(() => {
      document.title = "Reset Password";
    }, []);

    const [pass, setPass] = useState("")
    const [state, setState] = useState(0);

    const resetPassword = (e) => {
        e.preventDefault();
        if(pass !== ""){
            axios({
                method: "post",
                url: "api/user/resetpasswordon",
                data: { verif_id: id, password: pass}
              }).then((response) => {
                if(response.data.err){
                  console.log(response.data.err);
                  setState(2);
                } else {
                  setState(1);
                }
              })
        }
    }

    return (
      <div className="centerpage">
     
        <div className="centerpage-main card">

          {state === 0 &&
            <form onSubmit={resetPassword}>
                <h2>Entre ton nouveau mot de passe</h2>
                <input required minLength="8" type='password' placeholder="mot de passe" value={pass} onChange={e => setPass(e.target.value)} />
                <input required  type='password' placeholder="confirmez le mot de passe" autoComplete="new-password"
                  onChange={(e) => {
                    if (e.target.value !== pass) e.target.setCustomValidity('Les mots de passes ne correspondent pas.');
                    else e.target.setCustomValidity('');
                  }}/>
                <button className="btn" type="submit">Confirmer</button>
            </form>
          }

          {state === 1 && <>
            <h2>Mot de passe modifié ! Vous pouvez maintenant vous connecter.</h2>

            <Link className="btn" to="/login">Retour à la page de connexion</Link>
            </>
          }

          {state === 2 && <>
            <h2 style={{color: "red"}}>Le mot de passe n'a pas pu être modifié.</h2>

            <Link className="btn" to="/login">Retour à la page de connexion</Link>
            </>
          }

        </div>
      </div>
    )
}

export default ResetPasswordOn
