import React from 'react'
import './Profil.scss'
import { useParams, Link } from 'react-router-dom';
import { User, Topic, Class, Category, Document} from 'models';

import { FaEnvelope, FaEdit, FaRegCommentDots, FaRegEdit, FaRegFileAlt, FaRegStar, FaTrophy } from 'react-icons/fa';
import { RiQuillPenLine } from 'react-icons/ri';

import { UserContext } from "Context";
import { useContext, useEffect, useState } from 'react';

import UserIcon from 'components/UserIcon/UserIcon';
import { useHistory } from 'react-router';

const Profil = () => {

  const [stats, setStats] = useState({});
  const history = useHistory();
  let { id } = useParams();

  const [user] = useContext(UserContext);

  const currentUser = User.getUserById(parseInt(id));

  const classe = Class.getClassById(currentUser.class_id);
  const semestre = Category.getCategoryById(classe.semester_id);
  const groupName = classe.title + "S" + semestre.title.substr(semestre.title.length - 1, 1);

  const getStats = async () => {
    const result = await User.getStatsByID(id);
    setStats(result);
    console.log(result);
  }

  useEffect(() => {
    getStats();
  }, [id])


  const topics = Topic.data.filter(topic => parseInt(topic.user_id) === parseInt(id));
  const documents = Document.data.filter(doc => parseInt(doc.user_id) === parseInt(id));
  const recent = topics.concat(documents);
  recent.sort((b, a) => a.creation_date - b.creation_date);

  const DisplayContrib = () => {
    if (!recent.length) return <p className="nocontrib">
      Cet utilisateur n'a pas encore contribué.
    </p>

    return recent.map(topic => {
      const category = Category.getCategoryById(topic.category_id);
      const style = { background: category.background };

      if (topic.drive_key) {
        return <div style={style} onClick={() => history.push(`/categories/${category.path}`)} className="contrib-div">
          <p><FaRegFileAlt /></p><p>{topic.title}</p> <p>{topic.creation_date.toLocaleDateString()}</p>
        </div>
      }

      const title = topic.title.replace(" ?","").replace(/\s/g, '-')
      const url = `/forum/${topic.id}/${title}`;

      return <div style={style} onClick={() => history.push(url)} className="contrib-div">
        <p><RiQuillPenLine /></p><p>{topic.title}</p> <p>{topic.creation_date.toLocaleDateString()}</p>
      </div>
    })
  }
  return (
    <div className="profil page">


      <div className="profil-overview box">
        <div className="profil-infos">
            <UserIcon size={100} user_id={currentUser.id} full noname />
            <span>
              <p>{currentUser.first_name} {currentUser.last_name}</p>
              <p>{groupName}</p>
              <p>{currentUser.mail}</p>
            </span>
        </div>

        {/*<div className="badges">
          <FaTrophy />
          <FaTrophy />
          <FaTrophy />
          <FaTrophy />
          <FaTrophy />
        </div>*/}

        <div className="overview-btn">
          <Link className="btn" to="/private-messages"><FaEnvelope /></Link>
          {user.id === parseInt(id) && <Link className="btn" to="/me/edit"><FaEdit /></Link>}
        </div>
      </div>

      <div className="profil-datas">
        <div className="profil-stats box">
          <h1>Statistiques</h1>
          <div>
            <div className="stat-div"><p>Messages <FaRegCommentDots /></p> <p style={{color: "#5459DB"}}>{stats.nb_messages}</p></div>
            <div className="stat-div"><p>Topics <RiQuillPenLine /></p> <p style={{color: "#CD45A1"}}>{stats.nb_topic}</p></div>
            <div className="stat-div"><p>Documents <FaRegFileAlt /></p> <p style={{color: "#14CD3B"}}>{stats.nb_documents}</p></div>
            <div className="stat-div"><p>Topics notés <FaRegStar /></p> <p style={{color: "#FFD757"}}>{stats.nb_notes_topic}</p></div>
            <div className="stat-div"><p>Documents notés <FaRegStar /></p> <p style={{color: "#FD7278"}}>{stats.nb_notes_documents}</p></div>
          </div>
        </div>
        <div className="profil-posts box">
          <h1>Contributions</h1>
          
          
  
          <div>   
            {/*<div className="blur-top"></div>*/}
            <div className="wrapper">
            <DisplayContrib />
            </div>       
            
          </div>

          <div className="blur-bottom"></div>
          
        </div>
      </div>



    </div>
  )
}


export default Profil;
