const clearDate = d => {
    if (d == 'Invalid Date' || !d) d = Date.now();
    d = new Date(d);

    return d;
}

export const dateToString = (d) => {
    d = clearDate(d);

    let ye = new Intl.DateTimeFormat('fr', { year: 'numeric' }).format(d);
    let mo = new Intl.DateTimeFormat('fr', { month: 'short' }).format(d).replace('.','');
    let da = new Intl.DateTimeFormat('fr', { day: '2-digit' }).format(d);
  
    return `${da} ${mo} ${ye}`;
}

export const dateToHour = (d) => {
    d = clearDate(d);

    let hour = ('0'+d.getHours()).substr(-2);
    let minute = ('0'+d.getMinutes()).substr(-2);

    return `${hour}:${minute}`;
}

export const dateToDay = (d) => {
    d = clearDate(d);

    return d.getUTCDate() + " " + d.toLocaleString('default', { month: 'long' });
}