import './Admin.scss';
import AdminPanel from './AdminPanel/AdminPanel';
import { useState } from 'react';
import { AiFillCalendar, AiFillFolderOpen } from "react-icons/ai";
import { IoMdColorPalette } from "react-icons/io";
import { SiGoogleclassroom } from "react-icons/si";
import { FaUser } from "react-icons/fa";

const list = [
    {
        title: "calendar",
        link: 'api/calendar',
        keys: [
            { key: "id", type: "number" },
            { key: "title", type: "text" },
            { key: "type", type: "text" },
            { key: "link", type: "text" }
          ],
        Icon: AiFillCalendar
    },
    {
        title: "color",
        link: 'api/color',
        keys: [
            { key: "id", type: "number" },
            { key: "color", type: "color" },
            { key: "title", type: "text" },
        ],
        Icon: IoMdColorPalette
    },
    {
        title: "category",
        link: 'api/category',
        keys: [
            { key: "id", type: "number" },
            { key: "title", type: "text" },
            { key: "category_id", type: "_Semester" },
            { key: "color_id", type: "_Color" },
            { key: "icon", type: "img" },
            { key: "module", type: "text" },
            { key: "coef", type: "number" },
        ],
        Icon: AiFillFolderOpen
    },
    {
        title: "classes",
        link: 'api/class',
        keys: [
            { key: "id", type: "number" },
            { key: "department_id", type: "_Department" },
            { key: "semester_id", type: "_Semester" },
            { key: "title", type: "text" },
            { key: "ics_link", type: "text" },
        ],
        Icon: SiGoogleclassroom
    },
    {
        title: "User",
        link: 'api/user',
        keys: [
            { key: "id", type: "number" },
            { key: "class_id", type: "_Class" },
            { key: "last_name", type: "text" },
            { key: "first_name", type: "text" },
            { key: "mail", type: "text" },
            { key: "icon", type: "img" },
            { key: "user_privilege", type: "number" },
            { key: "tomuss", type: "text" },
        ],
        Icon: FaUser
    }
]
function Admin() {
    const [index, setIndex] = useState(-1);

    const DisplayList = () => {
        return list.map((item, i) => {
            return <div key={i} className='card' onClick={() => setIndex(i)}>
                <h2>{item.title}</h2>
                <item.Icon />
            </div>
        })
    }

    return <div className='admin page'> 
        {(index == -1) && <div className='admin-list'><DisplayList /></div>}
        {(index != -1) && <AdminPanel setIndex={setIndex} keys={list[index].keys} link={list[index].link} />}
    </div>
}

export default Admin;
