import './Header.scss';
import { useEffect, useContext } from 'react';
import { useLocation, Link, useHistory } from 'react-router-dom';
import icon from "assets/icon.png";

import { TreeContext, UserContext } from "Context";


const Header = () => {

  const location = useLocation();
  const [tree, setTree] = useContext(TreeContext);
  const [user] = useContext(UserContext);

  useEffect(() => {
    navigate();

    let current_page = tree?.find(item => document.location.pathname.includes(item.path) && item.path !== "/");
    document.title = current_page ? current_page.name : "ALED";

    //if (!current_page && document.location.pathname !== "/") history.push("/");

  }, [location]); // eslint-disable-line react-hooks/exhaustive-deps

  const navigate = () => {
    let operation = false;
    let url = window.location.pathname;
    const tab = tree?.map(item => {
      if (item.path.split("/")[1] === url.split("/")[1]) { 
        if (!item.selected) {
          item.selected = true;
          operation = true;
        }
      } else {
        item.selected = false;
      }
      return item;
    });

    if (operation) {
      setTree(tab);
    }
  }
  
  const DispayItems = (opt) => {
    return tree?.map((page,index) => {
      if (page.display !== opt.display) return null;
      const color = page.selected ? "--selected" : "";

      if (page.admin && user.user_privilege != 2) return null;

      return <div key={index} className={"link link" + color}>
        <Link onClick={() => navigate(page.path)} to={page.path}>
          <page.icon className={"link-icon link-icon" + color}/>
        </Link>
      </div>
    })
  }

  return (
    <div className="header">
      <div className="container container-icon">
        <Link to="/"><img src={icon} alt="logo-appli" /></Link>
      </div>

      <div className="container container-main">
        <DispayItems display={1} />
      </div>

      <div className="container container-main">
        <DispayItems display={2} />
      </div>
    </div>
  );
}

export default Header;